<script>
export default {
  data() {
    return{
      isCollapsed: false,
      stuId: ''
    }
  },
  created() {
    this.setIsCollapsed()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setIsCollapsed()
      })()
    }
  },
  methods: {
    setIsCollapsed() {
      var val = document.body.clientWidth
      const def = 1024 // 默认宽度
      if (val < def) {
        this.isCollapsed = true
      } else {
        this.isCollapsed = false
      }
    },
    async logout() {
      const { data: res } = await this.$http.post('user/logout')
      if(res.status === 600) this.$router.push('/login')
    },
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed
    }
  }
}
</script>

