<template>
  <div class="detail_container">
    <div class="detail_box">
      <div class="detail_title">
        <span>学校信息</span>
        <el-button  @click="dialogVisible = true" plain>修改信息</el-button>
      </div>
      <div class="detail_context">
        <el-table :show-header="false" :data="inforData" class="table">
          <el-table-column  prop="key" width="120px"></el-table-column>
          <el-table-column  prop="value" align="center"></el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog title="修改信息" :visible.sync="dialogVisible" :width="dialogWidth"
               :before-close="handleClose" center="true">
      <el-form class="detail_form" ref="detailFormRef" :model="school" :rules="detailFormRules"
               label-position="top" label-width="100px">
        <el-form-item label="校名" prop="schoolName">
          <el-input v-model="school.schoolName" placeholder="校名"></el-input>
        </el-form-item>
        <el-form-item label="学校简称" prop="abbrName">
          <el-input v-model="school.abbrName" placeholder="学校简称"></el-input>
        </el-form-item>
        <el-form-item label="单位地址" prop="address">
          <el-input v-model="school.address" placeholder="单位地址"></el-input>
        </el-form-item>
        <el-form-item label="主教练姓名" prop="chiefName">
          <el-input v-model="school.chiefName" placeholder="主教练姓名"></el-input>
        </el-form-item >
        <el-form-item label="手机号/固话号" prop="phone">
          <el-input v-model="school.phone" placeholder="手机号/固话号"></el-input>
        </el-form-item>
        <el-form-item label="发票单位" prop="billEnterprise">
          <el-input v-model="school.billEnterprise" placeholder="发票单位"></el-input>
        </el-form-item>
        <el-form-item label="邮编" prop="postcode">
          <el-input v-model="school.postcode" placeholder="邮编"></el-input>
        </el-form-item>
        <el-form-item label="税号" prop="taxNum">
          <el-input v-model="school.taxNum" placeholder="税号"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    var validatePhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入手机号/固话号'))
      } else {
        const reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/
        const reg2 = /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/
        if (reg.test(value) || reg2.test(value)) {
          callback()
        } else {
          return callback(new Error('请输入正确的手机号/固话号'))
        }
      }
    }
    return {
      inforData: [
        {
          key: '校名',
          value: ''
        },
        {
          key: '学校简称',
          value: ''
        },
        {
          key: '单位地址',
          value: ''
        },
        {
          key: '主教练姓名',
          value: ''
        },
        {
          key: '手机/固话',
          value: ''
        },
        {
          key: '发票单位',
          value: ''
        },
        {
          key: '邮编',
          value: ''
        },
        {
          key: '税号',
          value: ''
        }
      ],
      school: {
        schoolId: '',
        schoolName: '',
        abbrName: '',
        address: '',
        chiefName: '',
        phone: '',
        billEnterprise: '',
        postcode: '',
        taxNum: ''
      },
      dialogVisible: false,
      dialogWidth: 0,
      schoolName: '',
      detailFormRules: {
        schoolName: [
          { required: true, message: '请输入学校名称', trigger: 'change' }
        ],
        abbrName: [
          { required: true, message: '请输入学校简称', trigger: 'change' }
        ],
        address: [
          { required: true, message: '请输入单位地址', trigger: 'change' }
        ],
        chiefName: [
          { required: true, message: '请输入主教练姓名', trigger: 'change' }
        ],
        phone: [
          { required: true, validator: validatePhone, trigger: 'blur' }
        ],
        billEnterprise: [
          { required: true, message: '请输入发票单位', trigger: 'change' }
        ],
        postcode: [
          { required: true, message: '请输入邮编', trigger: 'change' }
        ],
        taxNum: [
          { required: true, message: '请输入税号', trigger: 'change' }
        ]
      }
    }
  },
  created() {
    this.school.schoolId = this.$store.getters.schoolId
    this.setDialogWidth()
    this.getSchoolInfor()
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '80%'
      } else {
        this.dialogWidth = '360px'
      }
    },
    save() {
      this.$confirm('确认保存当前修改？')
        .then(_ => {
          this.changeSchoolInfor()
        })
        .catch(_ => {})
    },
    cancel() {
      this.$confirm('确认放弃修改？')
        .then(_ => {
          this.dialogVisible = false
          this.$refs.detailFormRef.resetFields()
        })
        .catch(_ => {})
    },
    handleClose(done) {
      this.$confirm('确认放弃修改？')
        .then(_ => {
          done()
          this.$refs.detailFormRef.resetFields()
        })
        .catch(_ => {})
    },
    async getSchoolInfor() {
      const { data: res } = await this.$http.get('coach/school', { params: { schoolId: this.school.schoolId } })
      if(res.status === 605) {
        this.inforData[0].value = res.data.schoolName
        this.school.schoolName = res.data.schoolName
        this.inforData[1].value = res.data.abbrName
        this.school.abbrName = res.data.abbrName
        this.inforData[2].value = res.data.address
        this.school.address = res.data.address
        this.inforData[3].value = res.data.chiefName
        this.school.chiefName = res.data.chiefName
        this.inforData[4].value = res.data.phone
        this.school.phone = res.data.phone
        this.inforData[5].value = res.data.billEnterprise
        this.school.billEnterprise = res.data.billEnterprise
        this.inforData[6].value = res.data.postcode
        this.school.postcode = res.data.postcode
        this.inforData[7].value = res.data.taxNum
        this.school.taxNum = res.data.taxNum
      }
    },
    changeSchoolInfor() {
      this.$refs.detailFormRef.validate(async valid => {
        if(!valid) return
        const { data: res } = await this.$http.post('coach/school', this.$qs.stringify(this.school))
        if(res.status === 600) {
          this.$message.success('修改成功')
          this.dialogVisible = false
          this.getSchoolInfor()
        } else this.$message.error('修改失败')
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .detail_container{
    height: 100%;
    width: 100%;
  }
  @media screen and (max-width:480px) {
    .detail_box{
      width: 320px;
    }
    .table{
      max-width: 100%;
      margin-left: 0;
    }
  }
  @media screen and (min-width:480px) {
    .detail_box {
      width: 400px;
    }
    .table {
      width: 350px;
    }
  }
  .detail_box{
    height: auto;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    left: 50%;
    transform: translate(-50%, -0%);
    .detail_title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 20px;
      margin-top: 10px;
      >span{
        margin-left: 20px;
      }
      .el-button{
        margin-right: 20px;
      }
    }
    .detail_context{
      text-align: center;
      .table{
        width: 80%;
        margin-top: 20px;
        left: 50%;
        transform: translate(-50%, 0);
        box-sizing: border-box;
      }
    }
  }
  .detail_form {
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .el-select{
    width: 100%;
  }
</style>
