<template>
  <div class="container">
    <div class="box">
      <div class="title">
        <span>队伍管理</span>
        <div>
          <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                    @input="search" clearable="true"></el-input>
        </div>
      </div>
      <div class="shop">
        <el-button  @click="addDialogVisible = true" plain>添加队伍</el-button>
        <el-button  @click="Delete" plain>解散队伍</el-button>
      </div>
      <div class="teams">
        <el-checkbox-group v-model="checkList">
          <el-row :gutter="20" >
            <el-col :xs="24" :sm="6" v-for="(item, index) in t_teamlist" :key="index"><div class="team">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <el-checkbox :label = "item.id">{{item.teamName}}</el-checkbox>
                </div>
                <div  class="information">
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">队名（中文）</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.teamName}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">队名（英文）</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.foreignName}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">教练姓名</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.coachName}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">队员姓名（队长）</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.stuAName}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">队员姓名</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.stuBName}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">队员姓名</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.stuCName}}</span>
                    </el-col>
                  </el-row>
                </div>
              </el-card>
            </div></el-col>
          </el-row>
        </el-checkbox-group>
      </div>
    </div>
    <el-dialog title="添加队伍" :visible.sync="addDialogVisible" :width="dialogWidth" :before-close="handleClose" center="true">
      <el-form ref="addteamFormRef" :model="team" :rules="addteamFormRules" class="add_team_form">
        <el-form-item prop="teamName" label="队名（中文）">
          <el-input v-model="team.teamName" placeholder="队名（中文）"></el-input>
        </el-form-item>
        <el-form-item prop="foreignName" label="队名（英文）">
          <el-input v-model="team.foreignName" placeholder="队名（英文）"></el-input>
        </el-form-item>
        <el-form-item prop="coachId" label="教练">
          <el-select v-model="team.coachId" filterable placeholder="教练" @change="coachChange">
            <el-option v-for="item in coacheList" :key="item.id" :label="item.coachName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="stuAId" label="参赛队员1（队长）">
          <el-select v-model="team.stuAId" filterable placeholder="参赛队员1（队长）" @change="stuChange(team.stuAId,'A')">
            <el-option v-for="item in studentList" :key="item.id" :label="item.stuName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="stuBId" label="参赛队员2">
          <el-select v-model="team.stuBId" filterable placeholder="参赛队员2" @change="stuChange(team.stuBId,'B')">
            <el-option v-for="item in studentList" :key="item.id" :label="item.stuName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="stuCId" label="参赛队员3">
          <el-select v-model="team.stuCId" filterable placeholder="参赛队员3" @change="stuChange(team.stuCId,'C')">
            <el-option v-for="item in studentList" :key="item.id" :label="item.stuName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addDialogVisible: false,
      dialogWidth: 0,
      checkList: [],
      teamlist: [],
      t_teamlist: [],
      studentList: [],
      coacheList: [],
      searchStr: '',
      team: {
        teamName: '',
        foreignName: '',
        schoolId: '',
        coachId: '',
        coachName: '',
        stuAId: '',
        stuAName: '',
        stuBId: '',
        stuBName: '',
        stuCId: '',
        stuCName: ''
      },
      addteamFormRules: {
        teamName: [
          { required: true, message: '请输入队名（中文）', trigger: 'blur' }
        ],
        foreignName: [
          { required: true, message: '请输入队名（英文）', trigger: 'blur' }
        ],
        coachId: [
          { required: true, message: '请输入教练', trigger: 'blur' }
        ],
        stuAId: [
          { required: true, message: '请输入参赛队员1（队长）', trigger: 'blur' }
        ],
        stuBId: [
          { required: true, message: '请输入参赛队员2', trigger: 'blur' }
        ],
        stuCId: [
          { required: true, message: '请输入参赛队员3', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.team.schoolId = this.$store.getters.schoolId
    this.setDialogWidth()
    this.getTeamlist()
    this.getStudentINfor()
    this.getCoachINfor()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setDialogWidth()
      })()
    }
  },
  methods: {
    setDialogWidth () {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '80%'
      } else {
        this.dialogWidth = '360px'
      }
    },
    async getStudentINfor () {
      const { data: res } = await this.$http.get('coach/students', { params: { schoolId: this.team.schoolId } })
      if (res.status === 605) {
        this.studentList = res.data
      }
    },
    async getCoachINfor () {
      const { data: res } = await this.$http.get('coach/coaches', { params: { schoolId: this.team.schoolId } })
      if (res.status === 605) {
        this.coacheList = res.data
      }
    },
    async Delete () {
      if(this.checkList.length === 0) this.$message.error('请选择所要删除的队伍')
      else{
        for(var i in this.checkList) {
          const { data: res } = await this.$http.post('coach/delTeam', this.$qs.stringify({ teamId: this.checkList[i] }))
          if (res.status === 600) {
            this.$message.success('删除成功')
            this.getTeamlist()
          } else this.$message.error('删除失败')
        }
      }
    },
    async getTeamlist () {
      const { data: res } = await this.$http.get('coach/team', { params: { schoolId: this.team.schoolId } })
      if (res.status === 605) {
        this.teamlist = res.data
        this.t_teamlist = res.data
      }
    },
    save () {
      this.$confirm('确认组建该队伍？')
        .then(_ => {
          this.addTeam()
        })
        .catch(_ => {
        })
    },
    cancel () {
      this.$confirm('确认放弃组建该队伍？')
        .then(_ => {
          this.addDialogVisible = false
          this.$refs.addteamFormRef.resetFields()
        })
        .catch(_ => {
          this.$ref.addteamFormRef.resetFields()
        })
    },
    handleClose (done) {
      this.$confirm('确认放弃组建该队伍？')
        .then(_ => {
          done()
        })
        .catch(_ => {
        })
    },
    coachChange (val) {
      var obj = {}
      obj = this.coacheList.find(function (item) {
        return item.id === val
      })
      this.team.coachName = obj.coachName
    },
    stuChange (val, type) {
      var obj = {}
      obj = this.studentList.find(function (item) {
        return item.id === val
      })
      if (type === 'A') this.team.stuAName = obj.stuName
      else if (type === 'B') this.team.stuBName = obj.stuName
      else this.team.stuCName = obj.stuName
    },
    addTeam () {
      this.$refs.addteamFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('coach/team', this.$qs.stringify(this.team))
        if (res.status === 600) {
          this.$message.success('组队成功')
          this.addDialogVisible = false
          this.getTeamlist()
          this.$refs.addteamFormRef.resetFields()
        } else this.$message.error('组队失败')
      })
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.t_teamlist = []
          for(var i in this.teamlist) {
            if(JSON.stringify(this.teamlist[i]).search(this.searchStr) >= 0) {
              this.t_teamlist.push(this.teamlist[i])
            }
          }
        } else this.t_teamlist = this.teamlist
        return this.t_teamlist
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

