<template>
  <div class="detail_container">
    <div class="detail_box">
      <div class="detail_title">
        <span>个人信息</span>
        <el-button  @click="dialogVisible = true" plain>修改信息</el-button>
      </div>
      <div class="detail_context">
        <el-table :show-header="false" :data="inforData" class="stu_table">
          <el-table-column  prop="key" width="80px"></el-table-column>
          <el-table-column  prop="value" align="center"></el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog title="修改信息" :visible.sync="dialogVisible" :width="dialogWidth"
               :before-close="handleClose" center="true">
      <el-form class="detail_form" ref="detailFormRef" :model="student" :rules="detailFormRules"
               label-position="top" label-width="100px">
        <el-form-item label="性别" prop="sex">
          <el-radio v-model="student.sex" label="男">男</el-radio>
          <el-radio v-model="student.sex" label="女">女</el-radio>
        </el-form-item>
        <el-form-item label="英文名" prop="firstName">
          <el-input v-model="student.firstName" placeholder="英文名"></el-input>
        </el-form-item>
        <el-form-item label="英文姓" prop="familyName">
          <el-input v-model="student.familyName" placeholder="英文姓"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="student.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item label="学号" prop="stuId">
          <el-input v-model="student.stuId" placeholder="学号"></el-input>
        </el-form-item >
        <el-form-item label="院系" prop="college">
          <el-input v-model="student.college" placeholder="院系"></el-input>
        </el-form-item>
        <el-form-item label="专业" prop="major">
          <el-input v-model="student.major" placeholder="专业"></el-input>
        </el-form-item>
        <el-form-item label="入学年份" prop="enrollYear" >
          <el-date-picker class="date-picker" v-model="student.enrollYear" type="year" value-format="yyyy"
                          placeholder="入学年份" ></el-date-picker>
        </el-form-item>
        <el-form-item label="T恤尺码" prop="size">
          <el-select v-model="student.size" filterable placeholder="T恤尺码">
            <el-option
              v-for="item in sizes" :key="item.value" :label="item.label" :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inforData: [
        {
          key: '中文姓名',
          value: ''
        },
        {
          key: '学号',
          value: ''
        },
        {
          key: '英文姓',
          value: ''
        },
        {
          key: '英文名',
          value: ''
        },
        {
          key: '学校',
          value: ''
        },
        {
          key: '院系',
          value: ''
        },
        {
          key: '专业',
          value: ''
        },
        {
          key: '入学年份',
          value: ''
        },
        {
          key: '电话',
          value: ''
        },
        {
          key: '邮箱',
          value: ''
        },
        {
          key: '性别',
          value: ''
        },
        {
          key: 'T恤尺码',
          value: ''
        }
      ],
      student: {
        id: '',
        stuId: '',
        stuName: '',
        familyName: '',
        firstName: '',
        college: '',
        major: '',
        enrollYear: '2016',
        email: '',
        sex: '',
        size: ''
      },
      sizes: [
        {
          value: 1,
          label: 'XS'
        },
        {
          value: 2,
          label: 'S'
        },
        {
          value: 3,
          label: 'M'
        },
        {
          value: 4,
          label: 'L'
        },
        {
          value: 5,
          label: 'XL'
        },
        {
          value: 6,
          label: 'XXL'
        },
        {
          value: 7,
          label: 'XXXL'
        }],
      schools: [],
      dialogVisible: false,
      dialogWidth: 0,
      detailFormRules: {
        firstName: [
          { required: true, message: '请输入英文名', trigger: 'change' }
        ],
        familyName: [
          { required: true, message: '请输入英文姓', trigger: 'change' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'change' },
          { type: 'email', message: '请输入正确的邮箱', trigger: 'change' }
        ],
        stuId: [
          { required: true, message: '请输入学号', trigger: 'change' }
        ],
        enrollYear: [
          { required: true, message: '请输入入学年份', trigger: 'change' }
        ],
        college: [
          { required: true, message: '请输入学院', trigger: 'change' }
        ],
        major: [
          { required: true, message: '请输入专业', trigger: 'change' }
        ],
        size: [
          { required: true, message: '请输入T恤尺码', trigger: 'change' }
        ]
      }
    }
  },
  created() {
    this.student.id = this.$store.getters.studentId
    this.setDialogWidth()
    this.getStudentInfor()
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '80%'
      } else {
        this.dialogWidth = '360px'
      }
    },
    save() {
      this.$confirm('确认保存当前修改？')
        .then(_ => {
          this.changeStuInfor()
        })
        .catch(_ => {})
    },
    cancel() {
      this.$confirm('确认放弃修改？')
        .then(_ => {
          this.dialogVisible = false
          this.$refs.detailFormRef.resetFields()
        })
        .catch(_ => {})
    },
    handleClose(done) {
      this.$confirm('确认放弃修改？')
        .then(_ => {
          done()
          this.$refs.detailFormRef.resetFields()
        })
        .catch(_ => {})
    },
    async getSchoolList() {
      const { data: res } = await this.$http.get('user/school')
      if(res.status === 605) {
        this.schools = res.data
      }
    },
    async getStudentInfor() {
      const { data: res } = await this.$http.get('stu/info', { params: { studentId: this.student.id } })
      if(res.status === 605) {
        this.inforData[0].value = res.data.stuName
        this.student.stuName = res.data.stuName
        this.inforData[1].value = res.data.stuId
        this.student.stuId = res.data.stuId
        this.inforData[2].value = res.data.familyName
        this.student.familyName = res.data.familyName
        this.inforData[3].value = res.data.firstName
        this.student.firstName = res.data.firstName
        this.inforData[4].value = res.data.schoolId
        this.inforData[5].value = res.data.college
        this.student.college = res.data.college
        this.inforData[6].value = res.data.major
        this.student.major = res.data.major
        this.inforData[7].value = res.data.enrollYear.toString()
        this.student.enrollYear = res.data.enrollYear.toString()
        this.inforData[8].value = res.data.phone
        this.inforData[9].value = res.data.email
        this.student.email = res.data.email
        this.inforData[10].value = res.data.sex
        this.student.sex = res.data.sex
        this.inforData[11].value = res.data.size
        this.student.size = res.data.size
      }
    },
    async changeStuInfor() {
      this.student.enrollYear.toString()
      const { data: res } = await this.$http.post('stu/info', this.$qs.stringify(this.student))
      if(res.status === 600) {
        this.$message.success('修改成功')
        this.dialogVisible = false
        this.getStudentInfor()
      } else this.$message.error('修改失败')
    }
  }
}
</script>
