                    @input="search" clearable="true"></el-input>
        </div>
      </div>
      <div class="shop">
        <el-button  @click="getCoachInfor" plain>教师信息</el-button>
        <el-button  @click="getStudentInfor" plain>学生信息</el-button>
        <el-button  @click="agreeApplication" plain>同意学校注册申请</el-button>
        <el-button  @click="refuseApplication" plain>拒绝学校注册申请</el-button>
      </div>
      <div class="schools">
        <el-checkbox-group v-model="checkList">
          <el-row :gutter="20">
            <el-col :xs="24" :sm="6" v-for="(item, index) in s_schoolList" :key="index"><div class="school">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
