          <template slot-scope="scope">
            <el-tag :type="scope.row.checkStatus === 1 ? 'success' : 2 ? 'primary' : 'danger'"
                    disable-transitions>{{scope.row.checkStatus === 1 ? '已通过' : 2 ? '待审核' : '已驳回'}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog title="学生信息" :visible.sync="studentDialogVisible" width="90%">
      <el-table :data="students">
        <el-table-column prop="stuName" label="姓名" ></el-table-column>
        <el-table-column prop="familyName" label="英文姓" ></el-table-column>
        <el-table-column prop="firstName" label="英文名"></el-table-column>
        <el-table-column prop="college" label="院系" ></el-table-column>
        <el-table-column prop="major" label="专业"></el-table-column>
        <el-table-column prop="enrollYear" label="入学年份" ></el-table-column>
        <el-table-column prop="phone" label="手机号" width="150px"></el-table-column>
        <el-table-column prop="email" label="邮箱地址" width="300px"></el-table-column>
        <el-table-column prop="sex" label="性别"></el-table-column>
        <el-table-column prop="size" label="衣服尺寸" ></el-table-column>
        <el-table-column prop="checkStatus" label="审核状态" width="90px" align="center" filter-placement="bottom-end"
                         :filters="[{ text: '已通过', value: 1 }, { text: '待审核', value: 2 },
                       { text: '已驳回', value: 3 }]" :filter-method="filterTag" >
          <template slot-scope="scope">
            <el-tag :type="scope.row.checkStatus === 1 ? 'success' : 2 ? 'primary' : 'danger'"
                    disable-transitions>{{scope.row.checkStatus === 1 ? '已通过' : 2 ? '待审核' : '已驳回'}}</el-tag>
          </template>
        </el-table-column>
