<template>
  <div class="container">
    <div class="box">
      <div class="title">
        <span>团队赛管理</span>
        <div>
          <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                    @input="search" clearable="true"></el-input>
        </div>
      </div>
      <div class="shop">
        <el-button  @click="addDialogVisible = true" plain>添加竞赛</el-button>
        <el-button  @click="Delete" plain>删除竞赛</el-button>
        <el-button  @click="getTeamList" plain>查看参赛队伍</el-button>
        <el-button  @click="getTeamNumList" plain>比赛名额管理</el-button>
        <el-button  @click="getAwardList" plain>查看证书</el-button>
        <el-button  @click="upload" plain>上传证书</el-button>
      </div>
      <div class="contests">
        <el-checkbox-group v-model="checkList" :max="1">
          <el-row :gutter="20" >
            <el-col :xs="24" :sm="6" v-for="(item, index) in contestList" :key="index"><div class="contest">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <el-checkbox :label = "item">
                    <span class="all_label">{{item.contestTitle}}</span>
                  </el-checkbox>
                </div>
                <div  class="information">
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">比赛名称</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.contestTitle}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">开始时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.startTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">结束时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.endTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">报名开始时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.signStartTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">报名结束时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.signEndTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">学校</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.schoolId}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">负责人</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.chief}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">电话</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.phone}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">邮件地址</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.email}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">已报名队伍总计</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.numTeamMax}}</span>
                    </el-col>
                  </el-row>
                </div>
              </el-card>
            </div></el-col>
          </el-row>
        </el-checkbox-group>
      </div>
    </div>
    <el-dialog title="添加比赛" :visible.sync="addDialogVisible" :width="dialogWidth" :before-close="handleClose">
      <el-form ref="addContestFormRef" :model="contest" :rules="addContestFormRules" class="add_Contest_form">
        <el-form-item prop="contestTitle">
          <el-input v-model="contest.contestTitle" placeholder="竞赛名"></el-input>
        </el-form-item>
        <el-form-item prop="signStartTime">
          <el-date-picker class="date-picker" v-model="contest.signStartTime" type="date"
                          placeholder="报名开始时间" ></el-date-picker>
        </el-form-item>
        <el-form-item prop="signEndTime">
          <el-date-picker class="date-picker" v-model="contest.signEndTime" type="date"
                          placeholder="报名结束时间" ></el-date-picker>
        </el-form-item>
        <el-form-item prop="startTime">
          <el-date-picker class="date-picker" v-model="contest.startTime" type="date"
                          placeholder="开始时间" ></el-date-picker>
        </el-form-item>
        <el-form-item prop="endTime">
          <el-date-picker class="date-picker" v-model="contest.endTime" type="date"
                          placeholder="结束时间" ></el-date-picker>
        </el-form-item>
        <el-form-item prop="schoolID">
          <el-select v-model="contest.schoolId" filterable placeholder="学校">
            <el-option v-for="item in schools" :key="item.id" :label="item.schoolName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="chief">
          <el-input v-model="contest.chief" placeholder="负责人" ></el-input>
        </el-form-item>
        <el-form-item prop="phone">
          <el-input v-model="contest.phone" placeholder="手机号"></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input v-model="contest.email" placeholder="邮箱"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="参赛队伍信息" :visible.sync="lookDialogVisible" width="80%">
      <el-button  @click="downLoadExcel" plain>导出参赛队伍信息</el-button>
      <el-table :data="teamList">
        <el-table-column prop="teamName" label="队伍名称" ></el-table-column>
        <el-table-column prop="foreignName" label="队伍英文名称"></el-table-column>
        <el-table-column prop="打星" label="队伍类型" >{{false ? '正常参加' : '打星参加'}}</el-table-column>
        <el-table-column prop="coachName" label="教练姓名"></el-table-column>
        <el-table-column prop="stuAName" label="队员姓名（队长）" ></el-table-column>
        <el-table-column prop="stuBName" label="队员姓名" ></el-table-column>
        <el-table-column prop="stuCName" label="队员姓名" ></el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog title="参赛名额管理" :visible.sync="manageDialogVisible" width="360px" >
      <el-input placeholder="请输入内容" v-model="searchSchoolStr" class="input-with-select" suffix-icon="el-icon-search"
                @input="searchSchool" clearable="true"></el-input>
      <el-table :data="t_teamNumList" @row-click="clearNum">
        <el-table-column prop="schoolName" label="参赛学校名称" ></el-table-column>
        <el-table-column prop="numTeamMax" label="参赛名额" align="center">
          <template slot-scope="scope">
            <el-popover placement="right">
              <el-input :placeholder="scope.row.numTeamMax" v-model="newNum" class="input-with-select" size="small" style="width: 130px">
                <el-button slot="append" icon="el-icon-document-checked" size="mini" @click="saveNum(newNum,scope.row)"></el-button>
              </el-input>
              <div slot="reference">{{ scope.row.numTeamMax }}</div>
            </el-popover>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog title="证书列表" :visible.sync="awardDialogVisible" :width="awardDialogWidth">
      <el-button  @click="downLoad" plain>批量下载</el-button>
      <el-table :data="awardList" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label="证书名称" ></el-table-column>
        <el-table-column prop="state" label="操作" width="100px" align="center">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="预览" placement="top">
              <el-button type="primary" icon="el-icon-view" @click="viewNews(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog title="上传证书" :visible.sync="uploadDialogVisible" :width="dialogWidth" :before-close="handleCloseUpload">
      <el-upload
        class="upload-demo"
        ref="upload"
        action="doUpload"
        :limit="1"
        :file-list="fileList"
        :before-upload="beforeUpload">
        <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
        <div slot="tip" class="el-upload-list__item-name">
          <div style="padding: 0;height: 25px; width: 330px">上传的文件请打包成<h4 style="display: inline-block; color: red">zip</h4>文件压缩包</div>
          <div style="padding: 0;height: 25px; width: 350px">压缩包内应是以<h4 style="display: inline-block; color: red">teamId</h4>
            命名的<h4 style="display: inline-block; color: red">pdf</h4>文件。</div>
          <el-divider style="display:block; width: 360px"></el-divider>
        </div>
        <div slot="tip" class="el-upload-list__item-name">{{fileName}}</div>
      </el-upload>
      <span slot="footer" class="dialog-footer">
       <el-button @click="cancelUpload()">取消</el-button>
        <el-button type="primary" @click="saveUpload()">上传</el-button>
      </span>
    </el-dialog>
    <el-dialog title="上传错误" :visible.sync="errorDialogVisible" :width="awardDialogWidth" :before-close="handleCloseError">
      <el-table :data="errorList" :width="tablewidth">
        <el-table-column prop="fileName" label="文件名" ></el-table-column>
        <el-table-column prop="error" label="错误信息"></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveError()">我知道了</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    var validateSignStartTime = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入报名开始时间'))
      } else if (this.contest.signEndTime !== '') {
        this.$refs.addContestFormRef.validateField('signEndTime')
      }
      callback()
    }
    var validateSignEndTime = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入报名结束时间'))
      } else if(Date.parse(value) < Date.parse(this.contest.signStartTime)) {
        callback(new Error('报名结束时间不得早于报名开始时间'))
      } else if (this.contest.startTime !== '') {
        this.$refs.addContestFormRef.validateField('startTime')
      }
      callback()
    }
    var validateStartTime = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入开始时间时间'))
      } else if(Date.parse(value) < Date.parse(this.contest.signEndTime)) {
        callback(new Error('开始时间不得早于报名结束时间'))
      }else if (this.contest.endTime !== '') {
        this.$refs.addContestFormRef.validateField('endTime')
      }
      callback()
    }
    var validateEndTime = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入结束时间'))
      } else if(Date.parse(value) < Date.parse(this.contest.startTime)) {
        callback(new Error('结束时间不得早于开始时间'))
      }
      callback()
    }
    var validatePhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入手机号'))
      } else {
        const reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/
        if (reg.test(value)) {
          callback()
        } else {
          return callback(new Error('请输入正确的手机号'))
        }
      }
    }
    return {
      addDialogVisible: false,
      lookDialogVisible: false,
      manageDialogVisible: false,
      awardDialogVisible: false,
      uploadDialogVisible: false,
      errorDialogVisible: false,
      dialogWidth: 0,
      tablewidth: 0,
      awardDialogWidth: 0,
      checkList: [],
      contestList: [],
      c_contestList: [],
      searchStr: '',
      searchSchoolStr: '',
      teamList: [],
      teamInfoList: [],
      teamNumList: [],
      t_teamNumList: [],
      awardList: [],
      checkedAwardList: [],
      errorList: [],
      contestNum: '',
      schools: [],
      visible: false,
      newNum: '',
      files: [],
      fileList: [],
      fileName: '',
      contest: {
        contestTitle: '',
        startTime: '',
        endTime: '',
        signStartTime: '',
        signEndTime: '',
        schoolId: '',
        chief: '',
        phone: '',
        email: ''
      },
      addContestFormRules: {
        phone: [
          { required: true, validator: validatePhone, trigger: 'blur' }
        ],
        contestTitle: [
          { required: true, message: '请输入比赛名称', trigger: 'blur' }
        ],
        startTime: [
          { required: true, validator: validateStartTime, trigger: 'blur' }
        ],
        endTime: [
          { required: true, validator: validateEndTime, trigger: 'blur' }
        ],
        signStartTime: [
          { required: true, validator: validateSignStartTime, trigger: 'blur' }
        ],
        signEndTime: [
          { required: true, validator: validateSignEndTime, trigger: 'blur' }
        ],
        schoolId: [
          { required: true, message: '请选择学校', trigger: 'blur' }
        ],
        chief: [
          { required: true, message: '请输入负责人姓名', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.setDialogWidth()
    this.getContestList()
    this.getSchoolList()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setDialogWidth()
      })()
    }
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '90%'
        this.tablewidth = 150
        this.awardDialogWidth = '90%'
      } else {
        this.dialogWidth = '360px'
        this.tablewidth = 200
        this.awardDialogWidth = '70%'
      }
    },
    async Delete() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        for (var i in this.checkList) {
          const { data: res } = await this.$http.post('admin/contest/delete', this.$qs.stringify({ contestId: this.checkList[i] }))
          if (res.status === 600) {
            this.$message.success('删除成功')
            this.getContestList()
            this.checkList = ''
          } else this.$message.error('删除失败')
        }
      }
    },
    async getContestList() {
      const { data: res } = await this.$http.get('admin/contest')
      if(res.status === 605) {
        this.contestList = res.data
        this.c_contestList = res.data
      }
    },
    async getSchoolList() {
      const { data: res } = await this.$http.get('user/school')
      if(res.status === 605) {
        this.schools = res.data
      }
    },
    async getAwardList() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        const { data: res } = await this.$http.get('contest/credential/info', { params: { contestId: this.checkList[0].id } })
        if (res.status === 605) {
          this.awardList = res.data
        }
        this.awardDialogVisible = true
      }
    },
    async getTeamList() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        const { data: res } = await this.$http.get('admin/teamContest', { params: { contestId: this.checkList[0].id } })
        if (res.status === 605) {
          this.teamList = res.data
        }
        this.lookDialogVisible = true
      }
    },
    async getTeamNumList() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        const { data: res } = await this.$http.get('admin/quota', {
          params: {
            contestId: this.checkList[0].id,
            schoolId: 0
          }
        })
        if (res.status === 605) {
          this.teamNumList = res.data
          this.t_teamNumList = res.data
          for (var i in this.t_teamNumList.length) {
            this.t_teamNumList[i].isOK = false
          }
        }
        this.manageDialogVisible = true
      }
    },
    formatDate (date) {
      date = new Date(date)
      var w = date.getDay()
      var arr = ['Sun', 'Mon', 'Tues', 'Wed', 'Thur', 'Fri', 'Sat']
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return arr[w] + ' ' + y + '-' + m + '-' + d
    },
    save() {
      this.$confirm('确认添加该比赛？')
        .then(_ => {
          this.addContest()
        })
        .catch(_ => {})
    },
    cancel() {
      this.$confirm('确认放弃添加比赛？')
        .then(_ => {
          this.addDialogVisible = false
          this.$refs.addContestFormRef.resetFields()
        })
        .catch(_ => {})
    },
    handleClose(done) {
      this.$confirm('确认放弃添加比赛？')
        .then(_ => {
          done()
          this.$refs.addContestFormRef.resetFields()
        })
        .catch(_ => {})
    },
    saveUpload() {
      this.$confirm('确认上传该文件？')
        .then(_ => {
          this.uploadAward()
        })
        .catch(_ => {})
    },
    saveError() {
      this.$confirm('确认关闭？')
        .then(_ => {
          this.errorDialogVisible = false
        })
        .catch(_ => {})
    },
    cancelUpload() {
      this.$confirm('确认放弃上传？')
        .then(_ => {
          this.uploadDialogVisible = false
          this.fileName = ''
          this.files = ''
        })
        .catch(_ => {})
    },
    handleCloseUpload(done) {
      this.$confirm('确认放弃上传？')
        .then(_ => {
          done()
          this.fileName = ''
          this.files = ''
        })
        .catch(_ => {})
    },
    handleCloseError(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    addContest() {
      this.$refs.addContestFormRef.validate(async valid => {
        if(!valid) return this.$message.error('信息填写错误')
        this.contest.signStartTime = this.contest.signStartTime.toString()
        this.contest.signEndTime = this.contest.signEndTime.toString()
        this.contest.startTime = this.contest.startTime.toString()
        this.contest.endTime = this.contest.endTime.toString()
        const { data: res } = await this.$http.post('admin/contest', this.$qs.stringify(this.contest))
        if(res.status === 600) {
          this.$message.success('添加成功')
          this.addDialogVisible = false
          this.getContestList()
          this.$refs.addContestFormRef.resetFields()
        }else this.$message.error('添加失败')
      })
    },
    async saveNum(num, row) {
      const { data: res } = await this.$http.post('admin/quota/update', this.$qs.stringify({
        schoolId: row.schoolId,
        contestId: this.checkList[0].id,
        num: num
      }))
      if (res.status === 600) {
        this.$message.success('修改成功')
        row.numTeamMax = num
      }else if(res.status === 723) {
        this.$message.error('该学校报名数已超过此限额')
      }else this.$message.error('修改失败')
    },
    async downLoadExcel() {
      const { data: res } = await this.$http.get('admin/contest/report', { params: { contestId: this.checkList[0].id } })
      if (res.status === 605) {
        this.teamInfoList = res.data
        // eslint-disable-next-line camelcase
        const { export_json_to_excel } = require('@/vendor/Export2Excel')
        const tHeader = ['队伍名称', '队伍英文名称', '学校', '学校英文名称',
          '教练姓名', '教练英文名', '教练电话', '教练邮箱', '教练是否到场', '教练队服尺码',
          '队长姓名', '队长英文名', '队长性别', '队长电话', '队长队服尺码',
          '队员1姓名', '队员1英文名', '队员1性别', '队员1电话', '队员1队服尺码',
          '队员2姓名', '队员2英文名', '队员2性别', '队员2电话', '队员2队服尺码']
        const filterVal = ['teamName', 'teamForeignName', 'schoolName', 'abbrName',
          'coachName', 'coachForeignName', 'phone', 'email', 'coachOnSite', 'coachSize',
          'stuAName', 'stuAForeignName', 'stuASex', 'stuAPhone', 'stuASize',
          'stuBName', 'stuBForeignName', 'stuBSex', 'stuBPhone', 'stuBSize',
          'stuCName', 'stuCForeignName', 'stuCSex', 'stuCPhone', 'stuCSize']
        const list = this.teamInfoList
        const data = this.formatJson(filterVal, list)
        export_json_to_excel(tHeader, data, this.checkList[0].contestTitle + '--参赛队伍信息')
      }
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    // async deleteAward() {
    //   const { data: res } = await this.$http.post('admin/contest/award/delete', { awardId: this.contestList[0].id })
    //   if(res.status === 600) this.$message.success('删除成功')
    //   this.$message.error('删除失败')
    // },
    upload() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else this.uploadDialogVisible = true
    },
    beforeUpload(file) {
      this.files = file
      const extension = file.name.split('.')[1] === 'zip'
      if (!extension) {
        this.$message.warning('上传文件只能是zip!')
        return
      }
      this.fileName = file.name
      return false
    },
    async uploadAward() {
      if(this.fileName === '') {
        this.$message.warning('请选择要上传的文件！')
        return false
      }
      var fileFormData = new FormData()
      fileFormData.append('file', this.files, this.fileName)
      fileFormData.append('contestId', this.contestList[0].id)
      var requestConfig = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      const { data: res } = await this.$http.post('admin/contest/credentials', fileFormData, requestConfig)
      if(res.status === 600) {
        if(res.data.fail.length !== 0) {
          this.errorList = res.data.fail
          this.errorDialogVisible = true
        } else {
          this.$message.success('上传成功')
          this.uploadDialogVisible = false
          this.fileName = ''
          this.files = ''
        }
      } else if(res.status === 750) {
        this.$message.error('zip压缩包非法')
      }else this.$message.error('上传失败')
    },
    async viewNews(id) {
      const { data: res } = await this.$http.get('contest/credential', { params: { id: id } })
      const link = document.createElement('a')
      link.href = res.data
      link.download = '' // 重命名文件
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    },
    handleSelectionChange(val) {
      this.checkedAwardList = val
    },
    async downLoad() {
      if(this.checkedAwardList.length === 0) this.$message.error('请选择要下载的奖状')
      else {
        var params = []
        for (var i = 0; i < this.checkedAwardList.length; i++) {
          params.push(this.checkedAwardList[i].id)
        }
        var a = 'http://47.100.89.70:10010/api/contest/credentials?ids=' + params
        const link = document.createElement('a')
        link.href = a
        link.download = '' // 重命名文件
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.c_contestList = []
          for(var i in this.contestList) {
            if(JSON.stringify(this.contestList[i]).search(this.searchStr) >= 0) {
              this.c_contestList.push(this.contestList[i])
            }
          }
        } else this.c_contestList = this.contestList
        return this.c_contestList
      }, 500)
    },
    searchSchool () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchSchoolStr !== '') {
          this.t_teamNumList = []
          for(var i in this.teamNumList) {
            if(JSON.stringify(this.teamNumList[i]).search(this.searchSchoolStr) >= 0) {
              this.t_teamNumList.push(this.teamNumList[i])
            }
          }
        } else this.t_teamNumList = this.teamNumList
        return this.t_teamNumList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    },
    clearNum() {
      this.newNum = ''
    }
  }
}
</script>
