<template>
  <div class="login_container">
    <div class="login_box">
      <div class="avatar_box">
        <img src="../../assets/logo.jpg" alt="">
      </div>
      <div class="login_title">
        <p>管理员登录</p>
      </div>
      <el-form ref="loginFormRef" :model="user" :rules="loginFormRules" class="login_form">
        <el-form-item prop="phone">
          <el-input v-model="user.phone" prefix-icon="el-icon-mobile" placeholder="手机号"></el-input>
        </el-form-item>
        <el-form-item prop="pswd" style="margin-bottom: 5px;">
          <el-input v-model="user.pswd" prefix-icon="el-icon-lock" placeholder="密码" type="password"
                    show-password="true"></el-input>
        </el-form-item>
        <el-form-item style="margin-bottom: 5px; padding: 0 20px;">
<!--          <el-checkbox v-model="checked">记住我</el-checkbox>-->
          <router-link :to="{path:'findPassword'}" class="link">忘记密码？</router-link>
        </el-form-item>
        <el-form-item>
          <el-button class="btns" type="primary" @click="login" round plain>登陆</el-button>
          <el-divider content-position="center">或</el-divider>
          <el-button class="btns" type="primary" @click="register" round plain>注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      user: {
        phone: '',
        pswd: ''
      },
      checked: '',
      loginFormRules: {
        phone: [
          {
            required: true,
            message: '请输入手机号码',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 15,
            message: '请输入正确的手机号码',
            trigger: 'blur'
          }
        ],
        pswd: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 15,
            message: '长度在 8 到 15 个字符',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted () {
    this.getCookie()
  },
  methods: {
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('user/adminLogin', this.$qs.stringify(this.user))
        if (res.status === 600) {
          this.$message.success('登陆成功')
          this.$store.commit('changeAdminId', res.data.id)
          if (this.checked === true) {
            this.setCookie()
          }
          this.$router.push('/management')
        } else if (res.status === 702) {
          this.$message.error('账号不存在')
        } else if (res.status === 703) {
          this.$message.error('密码错误')
        } else {
          this.$message.error('登陆失败')
        }
      })
    },
    setCookie () {
      var exdate = new Date()
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * 30)
      window.document.cookie =
        this.user.phone + '=' + this.user.phone + ';path=/;expires=' + exdate.toGMTString()
      window.document.cookie =
        this.user.phone + 'pswd=' + this.user.pswd + ';path=/;expires=' + exdate.toGMTString()
    },
    getCookie() {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split('; ')
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split('=')
          if (arr2[0] === this.user.phone) {
            this.user.phone = arr2[1]
          } else if (arr2[0] === this.user.phone + 'pswd') {
            this.user.pswd = arr2[1]
          }
        }
      }
    }
  }
}
</script>

