<template>
  <div class="container">
    <div class="box">
      <div class="news">
        <p class="news_title">通知公告</p>
        <p class="break">··········IMPORTANT NOTICE ··········</p>
        <div id="pc">
          <el-col :span="24" v-for="(item, index) in news" :key="index">
            <router-link :to=" 'noticeInfo/' + item.id">
              <div class="news">
                <el-card class="box-card" >
                  <div class="left">
                    <el-image  fit="cover" :src="item.coverUrl"></el-image>
                  </div>
                  <div class="right">
                    <div class="title">
                      <h1>{{item.title}}</h1>
                      <p  style="font-size: 15px; line-height: 0; color: #969896;">{{formatDate(item.latestEditTime)}}</p>
                      <div class="news_content">
                        <p>{{item.intro}}</p>
                      </div>
                    </div>
                  </div>
                </el-card>
              </div>
            </router-link>
          </el-col>
        </div>
        <div id="phone">
          <el-col :span="24" v-for="(item, index) in news" :key="index">
            <router-link :to=" 'noticeInfo/' + item.id">
              <div class="news">
                <el-card class="box-card" >
                  <div class="right">
                    <div class="title">
                      <el-image class="img" fit="cover" :src="item.coverUrl"></el-image>
                      <h4>{{item.title}}</h4>
                      <p class="date">{{formatDate(item.latestEditTime)}}</p>
                      <div class="news_content">
                        <p>{{item.intro}}</p>
                      </div>
                    </div>
                  </div>
                </el-card>
              </div>
            </router-link>
          </el-col>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      news: []
    }
  },
  mounted: function () {
    this.setHeight()
    this.getArticle()
  },
  methods: {
    setHeight () {
      var val = document.body.clientWidth
      const def = 1024 // 默认宽度
      if (val < def) {
        document.getElementById('pc').style.display = 'none'
      } else {
        document.getElementById('phone').style.display = 'none'
      }
    },
    async getArticle () {
      const { data: res } = await this.$http.get('user/article')
      if (res.status === 605) {
        var data = res.data.reverse()
        for (var i = 0; i < data.length; i++) {
          if (data[i].tag === 1) {
            this.news.push(data[i])
          }
        }
      }
    },
    formatDate (date) {
      date = new Date(date)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return y + '-' + m + '-' + d
    }
  }
}
</script>

