<template>
  <div class="detail_container">
    <div class="detail_box">
      <div class="detail_title">
        <span>个人信息</span>
        <el-button  @click="dialogVisible = true" plain>修改信息</el-button>
      </div>
      <div class="detail_context">
        <el-table :show-header="false" :data="inforData" class="table">
          <el-table-column  prop="key" width="80px"></el-table-column>
          <el-table-column  prop="value"  align="center"></el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog title="修改信息" :visible.sync="dialogVisible" :width="dialogWidth"
               :before-close="handleClose" center="true">
      <el-form class="detail_form" ref="detailFormRef" :model="coach" :rules="detailFormRules"
               label-position="top" label-width="100px">
        <el-form-item label="中文名" prop="coachName">
          <el-input v-model="coach.coachName" placeholder="中文名"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-radio v-model="coach.sex" label="男">男</el-radio>
          <el-radio v-model="coach.sex" label="女">女</el-radio>
        </el-form-item>
        <el-form-item label="英文名" prop="firstName">
          <el-input v-model="coach.firstName" placeholder="英文名"></el-input>
        </el-form-item>
        <el-form-item label="英文姓" prop="familyName">
          <el-input v-model="coach.familyName" placeholder="英文姓"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="coach.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item label="T恤尺码" prop="size">
          <el-select v-model="coach.size" filterable placeholder="T恤尺码">
            <el-option
              v-for="item in sizes" :key="item.value" :label="item.label" :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inforData: [
        {
          key: '中文姓名',
          value: ''
        },
        {
          key: '英文姓',
          value: ''
        },
        {
          key: '英文名',
          value: ''
        },
        {
          key: '手机号',
          value: ''
        },
        {
          key: '邮箱',
          value: ''
        },
        {
          key: '性别',
          value: ''
        },
        {
          key: 'T恤尺码',
          value: ''
        }
      ],
      sizes: [
        {
          value: 1,
          label: 'XS'
        },
        {
          value: 2,
          label: 'S'
        },
        {
          value: 3,
          label: 'M'
        },
        {
          value: 4,
          label: 'L'
        },
        {
          value: 5,
          label: 'XL'
        },
        {
          value: 6,
          label: 'XXL'
        },
        {
          value: 7,
          label: 'XXXL'
        }],
      coach: {
        id: '',
        coachName: '',
        familyName: '',
        firstName: '',
        email: '',
        sex: '',
        size: ''
      },
      sex: '',
      schools: [],
      dialogVisible: false,
      dialogWidth: 0,
      schoolName: '',
      detailFormRules: {
        coachName: [
          { required: true, message: '请输入中文名', trigger: 'change' }
        ],
        firstName: [
          { required: true, message: '请输入英文名', trigger: 'change' }
        ],
        familyName: [
          { required: true, message: '请输入英文姓', trigger: 'change' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'change' },
          { type: 'email', message: '请输入正确的邮箱', trigger: 'change' }
        ],
        size: [
          { required: true, message: '请选择T恤尺码', trigger: 'change' }
        ],
        sex: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ]
      }
    }
  },
  created() {
    this.coach.id = this.$store.getters.coachId
    this.setDialogWidth()
    this.getCoachInfor()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setDialogWidth()
      })()
    }
    this.getCoachInfor()
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '80%'
      } else {
        this.dialogWidth = '360px'
      }
    },
    save() {
      this.$confirm('确认保存当前修改？')
        .then(_ => {
          this.changeCoachInfor()
        })
        .catch(_ => {})
    },
    cancel() {
      this.$confirm('确认放弃修改？')
        .then(_ => {
          this.dialogVisible = false
          this.$refs.detailFormRef.resetFields()
        })
        .catch(_ => {})
    },
    handleClose(done) {
      this.$confirm('确认放弃修改？')
        .then(_ => {
          done()
          this.$refs.detailFormRef.resetFields()
        })
        .catch(_ => {})
    },
    async getSchoolList() {
      const { data: res } = await this.$http.get('user/school')
      if(res.status === 605) {
        this.schools = res.data
      }
    },
    async getCoachInfor() {
      const { data: res } = await this.$http.get('coach/info', { params: { coachId: this.coach.id } })
      if(res.status === 605) {
        this.coach.coachName = res.data.coachName
        this.inforData[0].value = res.data.coachName
        this.coach.familyName = res.data.familyName
        this.inforData[1].value = res.data.familyName
        this.inforData[2].value = res.data.firstName
        this.coach.firstName = res.data.firstName
        this.inforData[3].value = res.data.phone
        this.inforData[4].value = res.data.email
        this.coach.email = res.data.email
        this.inforData[5].value = res.data.sex
        this.coach.sex = res.data.sex
        this.inforData[6].value = res.data.size
        this.coach.size = res.data.size
      }
    },
    async changeCoachInfor() {
      const { data: res } = await this.$http.post('coach/info', this.$qs.stringify(this.coach))
      if(res.status === 600) {
        this.$message.success('修改成功')
        this.dialogVisible = false
        this.getCoachInfor()
      } else this.$message.error('修改失败')
    }
  }
}
</script>

<style lang="less" scoped>
  .detail_container{
    height: 100%;
    width: 100%;
  }
  @media screen and (max-width:480px) {
    .detail_box{
      width: 320px;
    }
    .table{
      width: 100%;
      margin-left: 0;
    }
  }
  @media screen and (min-width:480px) {
    .detail_box {
      width: 400px;
    }
    .table{
      width: 350px;
    }
  }
  .detail_box{
    height: auto;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    left: 50%;
    transform: translate(-50%, -0%);
    .detail_title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 20px;
      margin-top: 10px;
      >span{
        margin-left: 20px;
      }
      .el-button{
        margin-right: 20px;
      }
    }
    .detail_context{
      text-align: center;
      .table{
        left: 50%;
        width: 80%;
        margin-top: 20px;
        transform: translate(-50%, 0);
        box-sizing: border-box;
      }
    }
  }
  .detail_form {
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .el-select{
