<template>
  <div class="container">
    <div class="title">
      <span>新闻审核</span>
      <div>
        <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                  @input="search" clearable="true"></el-input>
      </div>
    </div>
    <el-card class="news_list" shadow="never" body-style="height: 580px">
      <el-table :data="a_articleList.slice((currentPage-1)*pagesize,currentPage*pagesize)" height="250" border style="width: 100%;min-height: 600px" >
        <el-table-column type="index" label="#" align="center"></el-table-column>
        <el-table-column prop="tag" label="文章种类" width="90px" align="center" filter-placement="bottom-end"
                         :filters="[{text: '新闻', value: 0}, {text: '公告', value: 1}]" :filter-method="filterType" >
          <template slot-scope="scope">
            <el-tag :type="scope.row.tag === 1 ? 'primary' : 'warning' "
                    disable-transitions>{{scope.row.tag === 0 ? '新闻' : '公告'}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="title" label="标题"></el-table-column>
        <el-table-column prop="latestEditTime" label="日期" sortable width="150px" align="center">
          <template slot-scope="scope">
            {{formatDate(scope.row.latestEditTime)}}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态" width="120px" align="center" filter-placement="bottom-end"
                         :filters="[{ text: '已发布', value: 1 }, { text: '待审核', value: 2 },{ text: '已驳回', value: 3 }]" :filter-method="filterTag" >
          <template slot-scope="scope">
            <el-tag :type="getType(scope.row.status)" disable-transitions>{{getTag(scope.row.status)}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="操作" width="300px" align="center">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="预览" placement="top">
              <el-button type="primary" icon="el-icon-view" @click="viewNews(scope.row.content)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="发布" placement="top">
              <el-button type="success" icon="el-icon-check" @click="agreeApplication(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="驳回" placement="top">
              <el-button type="warning" icon="el-icon-close" @click="refuseApplication(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button type="danger" icon="el-icon-delete" :disabled="isDisabled(scope.row.status)" @click="deleteArticle(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[20, 50, 100]"
                     :page-size="pagesize" :layout="layout" :total="a_articleList.length" small="small" pager-count="7"
                     style="padding: 15px 0px 30px 25px">
      </el-pagination>
    </el-card>
    <el-dialog :visible.sync="dialogVisible" width="70%">
      <div class="ql-container ql-snow">
        <div class="ql-editor">
          <div class="content" v-html="content"></div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      searchStr: '',
      dialogVisible: false,
      articleList: [],
      a_articleList: [],
      content: '',
      currentPage: 1,
      pagesize: '20',
      small: false,
      layout: 'total, sizes, prev, pager, next, jumper'
    }
  },
  created () {
    this.getArticle()
    this.setPage()
  },
  methods: {
    setPage() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.small = true
        this.layout = 'sizes, prev, pager, next'
      } else {
        this.small = false
        this.layout = 'total, sizes, prev, pager, next, jumper'
      }
    },
    handleSizeChange(val) {
      this.pagesize = val
    },
    handleCurrentChange(val) {
      this.currentPage = val
    },
    filterTag(value, row) {
      return row.status === value
    },
    filterType(value, row, column) {
      const property = column.property
      return row[property] === value
    },
    getType(status) {
      if(status === 1) return 'success'
      else if (status === 2) return 'primary'
      else return 'danger'
    },
    getTag(status) {
      if(status === 1) return '已发布'
      else if (status === 2) return '待审核'
      else return '已驳回'
    },
    async getArticle () {
      const { data: res } = await this.$http.get('user/article')
      if (res.status === 605) {
        this.articleList = res.data
        this.a_articleList = this.articleList
      }
    },
    formatDate (date) {
      date = new Date(date)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return y + '-' + m + '-' + d
    },
    isDisabled(status) {
      if(status === 1) return true
      else return false
    },
    viewNews(val) {
      this.dialogVisible = true
      this.content = val
    },
    async agreeApplication(article) {
      const { data: res } = await this.$http.post('admin/article/status', this.$qs.stringify({ articleId: article.id, status: 1 }))
      if(res.status === 600) {
        this.$message.success('发布成功')
        article.status = 1
      } else this.$message.error('发布失败')
    },
    async refuseApplication(article) {
      const { data: res } = await this.$http.post('admin/article/status', this.$qs.stringify({ articleId: article.id, status: 3 }))
      if(res.status === 600) {
        this.$message.success('驳回成功')
        article.status = 3
      } else this.$message.error('驳回失败')
    },
    async deleteArticle(articleId) {
      const { data: res } = await this.$http.post('admin/article/delete', this.$qs.stringify({ articleId: articleId }))
      if(res.status === 600) {
        this.$message.success('删除成功')
        this.getArticle()
      } else this.$message.error('删除失败')
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.a_articleList = []
          for(var i in this.articleList) {
            if(this.articleList[i].title.search(this.searchStr) >= 0) {
              this.a_articleList.push(this.articleList[i])
            }
          }
        } else this.a_articleList = this.articleList
        return this.a_articleList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  @media screen and (max-width:767px) {
    .news_list{
      width: 360px;
      left: 50%;
      transform: translate(-50%, -0%);
    }
  }
  @media screen and (min-width:768px) {
    .title{
      width: 500px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  .container{
    height: 100%;
    width: 100%;
    .title {
      font-size: 20px;
      margin-top: 10px;
      margin-bottom: 20px;
      span{
        font-size: 20px;
      }
      >div{
        width: 300px;
      }
    }
    .news_list{
      position: relative;
      height: 100%;
      background: #FFFFFF;
      .el-table{
        display: inline-block;
        margin-top: 20px;
        .el-button{
          padding: 6px 20px;
        }
      }
    }
  }
</style>
