<script>
export default {
  data() {
    return {
      stuId: '',
      searchStr: '',
      checkList: [],
      contestList: [],
      c_contestList: []
    }
  },
  created () {
    this.stuId = this.$store.getters.studentId
    this.getContests()
  },
  methods: {
    async getContests () {
      const { data: res } = await this.$http.get('stu/contest', { params: { studentId: this.stuId } })
      if (res.status === 605) {
        this.contestList = res.data
        this.c_contestList = res.data
      }
    },
    formatDate (date) {
      date = new Date(date)
      var w = date.getDay()
      if(w === 0) w = 'Sun'
      else if (w === 1) w = 'Mon'
      else if (w === 2) w = 'Tues'
      else if (w === 3) w = 'Wed'
      else if (w === 4) w = 'Thur'
      else if (w === 5) w = 'Fri'
      else w = 'Sat'
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return w + ' ' + y + '-' + m + '-' + d
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.c_contestList = []
          for(var i in this.contestList) {
            if(JSON.stringify(this.contestList[i]).search(this.searchStr) >= 0) {
              this.c_contestList.push(this.contestList[i])
            }
          }
        } else this.c_contestList = this.contestList
        return this.c_contestList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

