<template>
  <div class="container">
      <div class="box">
        <div class="title">
          <span>个人赛</span>
          <div>
            <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                      @input="search" clearable="true"></el-input>
          </div>
        </div>
        <div class="shop">
          <el-button  @click="getContestList" plain>{{name}}</el-button>
          <el-button  @click="signUp" plain>报名参赛</el-button>
          <el-button  @click="cancelsignUpContest" plain>取消报名参赛</el-button>
        </div>
        <div class="contests">
          <el-checkbox-group v-model="checkList">
            <el-row :gutter="20" >
              <el-col :xs="24" :sm="6" v-for="(item, index) in c_contestList" :key="index"><div class="contest">
                <el-card class="box-card">
                  <div slot="header" class="clearfix">
                    <el-checkbox :label = "item.id">{{item.title}}</el-checkbox>
                  </div>
                  <div  class="information">
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">比赛名称</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{item.title}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">开始时间</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{formatDate(item.startTime)}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">结束时间</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{formatDate(item.endTime)}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">报名开始时间</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{formatDate(item.signStartTime)}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">报名结束时间</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{formatDate(item.signEndTime)}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">学校</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{item.schoolId}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">负责人</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{item.chief}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">电话</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{item.phone}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">邮件地址</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{item.email}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">报名人数上限</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{item.numMax}}</span>
                      </el-col>
                    </el-row>
                    <el-row >
                      <el-col :span="12">
                        <span style="float: left;">已报名人数总计</span>
                      </el-col>
                      <el-col :span="12">
                        <span class="infor">{{item.numSignedIn}}</span>
                      </el-col>
                    </el-row>
                  </div>
                </el-card>
              </div></el-col>
            </el-row>
          </el-checkbox-group>
        </div>
      </div>
    <el-dialog title="报名参赛" :visible.sync="addDialogVisible" :width="dialogWidth" :before-close="handleClose" center="true">
      <h4>请选择参赛方式</h4>
      <el-radio v-model="isStarred" label="1">打星参赛</el-radio>
      <el-radio v-model="isStarred" label="0">正常参赛</el-radio>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addDialogVisible: false,
      dialogWidth: '',
      awardDialogVisible: false,
      awardDialogWidth: 0,
      searchStr: '',
      stuId: '',
      checkList: [],
      awardList: [],
      contestList: [],
      c_contestList: [],
      isStarred: '1',
      name: '我的比赛'
    }
  },
  created () {
    this.stuId = this.$store.state.studentId
    this.getContests()
    this.setDialogWidth()
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '80%'
        this.awardDialogWidth = '90%'
      } else {
        this.dialogWidth = '360px'
        this.awardDialogWidth = '90%'
      }
    },
    async getContests () {
      const { data: res } = await this.$http.get('solo')
      if (res.status === 605) {
        this.contestList = res.data
        this.c_contestList = res.data
      }
    },
    async getSiginInContestList () {
      const { data: res } = await this.$http.get('stu/solo')
      if (res.status === 605) {
        this.contestList = res.data
        this.c_contestList = res.data
      }
    },
    async getAwardList() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        const { data: res } = await this.$http.get('solo/credential/info', { params: { soloContestId: this.checkList[0] } })
        if (res.status === 605) {
          this.awardList = res.data
        }
        this.awardDialogVisible = true
      }
    },
    getContestList () {
      if(this.name === '我的比赛') {
        this.getSiginInContestList()
        this.name = '全部比赛'
      } else{
        this.getContests()
        this.name = '我的比赛'
      }
    },
    formatDate (date) {
      date = new Date(date)
      var w = date.getDay()
      if(w === 0) w = 'Sun'
      else if (w === 1) w = 'Mon'
      else if (w === 2) w = 'Tues'
      else if (w === 3) w = 'Wed'
      else if (w === 4) w = 'Thur'
      else if (w === 5) w = 'Fri'
      else w = 'Sat'
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return w + ' ' + y + '-' + m + '-' + d
    },
    save() {
      this.$confirm('确定报名该比赛？')
        .then(_ => {
          this.signUpContest()
        })
        .catch(_ => {})
    },
    cancel() {
      this.$confirm('确定取消添加或更改？')
        .then(_ => {
          this.addDialogVisible = false
        })
        .catch(_ => {})
    },
    handleClose(done) {
      this.$confirm('信息未提交，确定关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    signUp() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else this.addDialogVisible = true
    },
    async signUpContest() {
      const { data: res } = await this.$http.post('stu/solo/sign', this.$qs.stringify({
        soloContestId: this.checkList[0],
        isStarred: '1'
      }))
      if (res.status === 600) {
        this.$message.success('报名成功')
        this.addDialogVisible = false
      } else if(res.status === 723) {
        this.$message.error('报名未开放')
      } else if(res.status === 725) {
        this.$message.error('重复报名')
      }else if(res.status === 722) {
        this.$message.error('名额不足')
      } else if(res.status === 721) {
        this.$message.error('个人赛不存在')
      } else this.$message.error('报名失败')
    },
    async cancelsignUpContest() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        const { data: res } = await this.$http.post('stu/solo/signOff', this.$qs.stringify({ soloContestId: this.checkList[0] }))
        if (res.status === 600) {
          this.$message.success('取消成功')
        } else if(res.status === 724) {
          this.$message.error('该学生未报名，退赛失败')
        } else if(res.status === 721) {
          this.$message.error('个人赛不存在')
        } else this.$message.error('取消失败')
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.c_contestList = []
          for(var i in this.contestList) {
            if(JSON.stringify(this.contestList[i]).search(this.searchStr) >= 0) {
              this.c_contestList.push(this.contestList[i])
            }
          }
        } else this.c_contestList = this.contestList
        return this.c_contestList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

