<template>
  <div class="container">
    <div class="box">
      <div class="title">
        <span>学生管理</span>
        <div>
          <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                    @input="search" clearable="true"></el-input>
        </div>
      </div>
      <div class="shop">
        <el-button  @click="passStuList" plain>批量通过</el-button>
        <el-button  @click="rejectStuList" plain>批量驳回</el-button>
      </div>
      <div class="students">
        <el-table :data="s_studentList.slice((currentPage-1)*pagesize,currentPage*pagesize)" border header-row-class-name="tableHead" :row-style="{height:0+'px'}"
                  :cell-style="{padding: 3+'px'}" :current-page.sync="currentPage" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="stuId" label="学号" align="center" width="90px" fixed="left"></el-table-column>
          <el-table-column prop="stuName" label="姓名" align="center" fixed="left"></el-table-column>
          <el-table-column prop="familyName" label="英文姓" align="center"></el-table-column>
          <el-table-column prop="firstName" label="英文名" align="center" width="120px"></el-table-column>
          <el-table-column prop="college" label="院系" align="center" width="200px"></el-table-column>
          <el-table-column prop="major" label="专业" align="center" width="200px"></el-table-column>
          <el-table-column prop="enrollYear" label="入学年份" align="center"></el-table-column>
          <el-table-column prop="phone" label="手机号" align="center" width="120px"></el-table-column>
          <el-table-column prop="email" label="邮箱地址" align="center" width="200px"></el-table-column>
          <el-table-column prop="sex" label="性别" align="center"></el-table-column>
          <el-table-column prop="size" label="衣服尺寸" align="center"></el-table-column>
          <el-table-column prop="checkStatus" label="审核状态" width="90px" align="center" filter-placement="bottom-end"
                           :filters="[{ text: '已通过', value: 1 }, { text: '待审核', value: 2 },
                       { text: '已驳回', value: 3 }]" :filter-method="filterTag" fixed="right">
            <template slot-scope="scope">
              <el-tag :type="getType(scope.row.checkStatus)" disable-transitions>{{getTag(scope.row.checkStatus)}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="state" label="操作" align="center" width="150px" fixed="right">
            <template slot-scope="scope">
              <el-button type="success" @click="passStu(scope.row)">通过</el-button>
              <el-button type="danger" @click="rejectStu(scope.row)">驳回</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[20, 50, 100]"
                       :page-size="pagesize" :layout="layout" :total="s_studentList.length" small="small" pager-count="7"
                       style="padding: 15px 0px 30px 25px">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return{
      schoolId: '',
      studentList: [],
      s_studentList: [],
      checkedStudentList: [],
      searchStr: '',
      currentPage: 1,
      pagesize: '20',
      small: false,
      layout: 'total, sizes, prev, pager, next, jumper'
    }
  },
  created () {
    this.schoolId = this.$store.getters.schoolId
    this.getStudentINfor()
    this.setPage()
  },
  methods: {
    setPage() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.small = true
        this.layout = 'sizes, prev, pager, next'
      } else {
        this.small = false
        this.layout = 'total, sizes, prev, pager, next, jumper'
      }
    },
    handleSizeChange(val) {
      this.pagesize = val
    },
    handleCurrentChange(val) {
      this.currentPage = val
    },
    filterTag(value, row) {
      return row.checkStatus === value
    },
    filterType(value, row, column) {
      const prop = column.prop
      return row[prop] === value
    },
    getType(checkStatus) {
      if(checkStatus === 1) return 'success'
      else if (checkStatus === 2) return 'primary'
      else return 'danger'
    },
    getTag(checkStatus) {
      if(checkStatus === 1) return '已通过'
      else if (checkStatus === 2) return '待审核'
      else return '已驳回'
    },
    async getStudentINfor() {
      const { data: res } = await this.$http.get('coach/students', { params: { schoolId: this.schoolId } })
      if(res.status === 605) {
        this.studentList = res.data
        this.s_studentList = this.studentList
      }
    },
    handleSelectionChange(val) {
      this.checkedStudentList = val
    },
    passStu(student) {
      var params = student.id
      this.pass(params, student)
    },
    rejectStu(student) {
      var params = student.id
      this.reject(params, student)
    },
    passStuList() {
      var param = []
      for (var i = 0; i < this.checkedStudentList.length; i++) {
        param.push(this.checkedStudentList[i].id)
      }
      var params = param + ''
      var student = ''
      this.pass(params, student)
    },
    rejectStuList() {
      var param = []
      for (var i = 0; i < this.checkedStudentList.length; i++) {
        param.push(this.checkedStudentList[i].id)
      }
      var params = param + ''
      var student = ''
      this.reject(params, student)
    },
    async pass(params, student) {
      const { data: res } = await this.$http.post('coach/passStu', this.$qs.stringify({ studentIds: params }))
      if(res.status === 600) {
        this.$message.success('审核成功')
        if(student === '') this.getStudentINfor()
        else student.checkStatus = 1
      }else{
        this.$message.error('审核失败')
      }
    },
    async reject(params, student) {
      const { data: res } = await this.$http.post('coach/rejectStu', this.$qs.stringify({ studentIds: params }))
      if(res.status === 600) {
        this.$message.success('审核成功')
        if(student === '') this.getStudentINfor()
        else student.checkStatus = 3
      }else{
        this.$message.error('审核失败')
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.s_studentList = []
          for(var i in this.studentList) {
            if(JSON.stringify(this.studentList[i]).search(this.searchStr) >= 0) {
              this.s_studentList.push(this.studentList[i])
            }
          }
        } else this.s_studentList = this.studentList
        return this.s_studentList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>
<style>
  tableHead {
    font-size: 10px;
  }
</style>
