import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../components/Home/Index'
import Login from '../components/Home/Login'
import Student from '../components/student/Student'
import Details from '../components/student/Details'
import Register from '../components/Home/Register'
import Contests from '../components/student/Contests'
import Changepwd from '../components/student/Changepwd'
import Management from '../components/manager/Management'
import AdminLogin from '../components/manager/AdminLogin'
import SchoolManagement from '../components/manager/SchoolManagement'
import ContestManagement from '../components/manager/ContestManagement'
import FindPassword from '../components/Home/FindPassword'
import Coach from '../components/coach/Coach'
import Home from '../components/Home/Home'
import News from '../components/Home/News'
import Notices from '../components/Home/Notices'
import NewsInfo from '../components/Home/NewsInfo'
import NoticeInfo from '../components/Home/NoticeInfo'
import NewsManagement from '../components/manager/NewsManagement'
import NewsEdit from '../components/manager/NewsEdit'
import MyNews from '../components/manager/MyNews'
import MyNewsList from '../components/manager/MyNewsList'
import CoachDetials from '../components/coach/CoachDetials'
import ChangeCoachPwd from '../components/coach/ChangeCoachPwd'
import StudentList from '../components/coach/StudentList'
import TeamsList from '../components/coach/TeamList'
import CoachList from '../components/coach/CoachList'
import ContestList from '../components/coach/ContestList'
import BillList from '../components/coach/BillList'
import SchoolDetails from '../components/coach/SchoolDetails'
import BillsManagement from '../components/manager/BillsManagement'
import SoloContestManagement from '../components/manager/SoloContestManagement'
import SoloContestList from '../components/coach/SoloContestList'
import SoloContests from '../components/student/SoloContests'

Vue.use(VueRouter)

const routes = [
  {
    path: '/index',
    component: Index,
    redirect: '/home',
    children: [
      {
        path: '/home',
        component: Home
      },
      {
        path: '/news',
        component: News
      },
      {
        path: '/notices',
        component: Notices
      },
      {
        path: '/noticeInfo/:id',
        component: NoticeInfo
      },
      {
        path: '/newsInfo/:id',
        component: NewsInfo
      },
      {
        path: '/login',
        component: Login
      },
      {
        path: '/adminLogin',
        component: AdminLogin
      },
      {
        path: '/register',
        component: Register
      },
      {
        path: '/findPassword',
        component: FindPassword
      }
    ]
  },
  {
    path: '/student',
    component: Student,
    redirect: '/student/details',
    children: [
      {
        path: '/student/details',
        component: Details
      },
      {
        path: '/student/contests',
        component: Contests
      },
      {
        path: '/student/changepwd',
        component: Changepwd
      },
      {
        path: '/student/soloContests',
        component: SoloContests
      }
    ]
  },
  {
    path: '/coach',
    component: Coach,
    redirect: '/coach/detials',
    children: [
      {
        path: '/coach/detials',
        component: CoachDetials
      },
      {
        path: '/coach/changePwd',
        component: ChangeCoachPwd
      },
      {
        path: '/coach/studentList',
        component: StudentList
      },
      {
        path: '/coach/teamList',
        component: TeamsList
      },
      {
        path: '/coach/coachList',
        component: CoachList
      },
      {
        path: '/coach/contestList',
        component: ContestList
      },
      {
        path: '/coach/billList',
        component: BillList
      },
      {
        path: '/coach/schoolDetails',
        component: SchoolDetails
      },
      {
        path: '/coach/soloContestList',
        component: SoloContestList
      }
    ]
  },
  {
    path: '/management',
    component: Management,
    redirect: '/management/myNews',
    children: [
      {
        path: '/management/schoolManagement',
        component: SchoolManagement
      },
      {
        path: '/management/contestManagement',
        component: ContestManagement
      },
      {
        path: '/management/newsManagement',
        component: NewsManagement
      },
      {
        path: '/management/billsManagement',
        component: BillsManagement
      },
      {
        path: '/management/soloContestManagement',
        component: SoloContestManagement
      },
      {
        path: '/management/myNews',
        component: MyNews,
        children: [
          {
            path: '/management/myNews',
            components: {
              default: MyNewsList,
              second: NewsEdit
            }
          }
        ]
      }
    ]
  },
  {
    path: '/',
    redirect: '/index'
  }
]

const router = new VueRouter({
  mode: 'hash',
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

export default router
