<template>
  <div class="changepwd_container">
    <div class="changepwd_box">
      <div class="changepwd_form">
        <el-form class="user-account-key" ref="passwordFormRef" :model="change" :rules="rules" label-width="100px">
          <el-form-item label="原密码" prop="oldPassword">
            <el-input type="password" placeholder="请输入原密码" v-model="change.oldPassword"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="newPassword">
            <el-input type="password" placeholder="请设置新密码" v-model="change.newPassword"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkNewPassword">
            <el-input type="password" placeholder="请确认新密码" v-model="change.checkNewPassword"></el-input>
          </el-form-item>
        </el-form>
        <div class="btn">
          <el-button type="primary" @click="onSubmit">保存</el-button>
          <el-button @click="$refs.passwordFormRef.resetFields()">重置</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var validateOldPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入原密码'))
      } else if(this.change.newPassword !== '') {
        this.$refs.passwordFormRef.validateField('newPassword')
      }
      callback()
    }
    var validateNewPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入新密码'))
      } else if(value === this.change.oldPassword) {
        callback(new Error('新密码与原密码相同'))
      } else if (this.change.checkNewPassword !== '') {
        this.$refs.passwordFormRef.validateField('checkNewPassword')
      }
      callback()
    }
    var validateCheckNewPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请确认新密码'))
      } else if (value !== this.change.newPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      change: {
        studentId: '',
        oldPassword: '',
        newPassword: '',
        checkNewPassword: ''
      },
      rules: {
        oldPassword: [
          { required: true, validator: validateOldPassword, trigger: 'blur' }
        ],
        newPassword: [
          { required: true, validator: validateNewPassword, trigger: 'blur' }
        ],
        checkNewPassword: [
          { required: true, validator: validateCheckNewPassword, trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.change.studentId = this.$store.getters.studentId
  },
  methods: {
    onSubmit () {
      this.$refs.passwordFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post('stu/password',
            this.$qs.stringify({ studentId: this.change.studentId, oldPassword: this.change.oldPassword, newPassword: this.change.newPassword }))
          if(res.status === 600) {
            this.$message.success('密码已修改')
            this.$refs.passwordFormRef.resetFields()
          } else {
            this.$message.error('密码修改失败')
            this.$refs.passwordFormRef.resetFields()
          }
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .changepwd_container{
    height: 100%;
  }
  @media screen and (max-width:480px) {
    .changepwd_box{
      width: 320px;
    }
  }
  @media screen and (min-width:480px) {
    .changepwd_box{
      width: 450px;
    }
  }
  .changepwd_box{
    height: auto;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    left: 50%;
    transform: translate(-50%, -0%);
    .changepwd_form{
      margin-top: 20px;
    }
  }
  .el-form{
    margin-right: 40px;
    margin-top: 30px;
    margin-left: 10px;
  }
  .el-form-item{
    margin-bottom: 30px;
  }
  .btn{
    margin-bottom: 30px;
    text-align: center;
  }
</style>
