<template>
    <div class="container">
      <div class="box">
        <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
          <el-tab-pane name="first">
            <span slot="label"><i class="el-icon-date"></i>我的新闻</span>
            <router-view v-if="isFirst"></router-view>
          </el-tab-pane>
          <el-tab-pane name="second">
            <span slot="label"><i class="el-icon-date"></i> 新闻编辑</span>
            <router-view v-if="isSecond" name="second"></router-view>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      activeName: 'first',
      isFirst: true,
      isSecond: false,
      article: {
        id: '',
        title: '',
        intro: '',
        content: '',
        coverUrl: '',
        adminId: [],
        tag: '0'
      }
    }
  },
  computed: {
    listData() {
      return this.$store.state.componentName
    }
  },
  watch: {
    listData (newVal, oldVal) {
      this.activeName = this.$store.state.componentName
      if (this.activeName === 'first') {
        this.$store.commit('changeComponentName', 'first')
        this.isFirst = true
        this.isSecond = false
      } else if (this.activeName === 'second') {
        this.isFirst = false
        this.isSecond = true
      }
    }
  },
  methods: {
    handleClick(tab) {
      if (tab.name === 'first') {
        this.$store.commit('changeComponentName', 'first')
        this.$store.commit('changeArticle', this.article)
        this.isFirst = true
        this.isSecond = false
      } else if (tab.name === 'second') {
        this.isFirst = false
        this.isSecond = true
        this.$store.commit('changeArticle', this.article)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .container{
    width: 100%;
    height: 100%;
  }
  @media screen and (max-width:480px) {
    .el-tabs{
      width: 360px;
      left: 50%;
      transform: translate(-50%, -0%);
    }
  }
  @media screen and (min-width:480px) {
    .el-tabs{
      min-height: 100%;
      margin-left: 20px;
      margin-right: 20px;
    }
  }
  .box{
    .el-tabs{
      margin-top: 20px;
      position: relative;
    }
  }
</style>
