<template>
  <div class="container">
    <div class="box">
      <div class="title">
        <span>教练管理</span>
        <div>
          <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                    @input="search" clearable="true"></el-input>
        </div>
      </div>
      <div class="shop">
        <el-button  @click="passCoachList" plain>批量通过</el-button>
        <el-button  @click="rejectCoachList" plain>批量驳回</el-button>
      </div>
      <div class="coaches">
        <el-table :data="c_coachList.slice((currentPage-1)*pagesize,currentPage*pagesize)" border style="width: 100%" header-row-class-name="tableHead" row-style="{height:0+'px'}"
                  :cell-style="{padding: 5+'px'}" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="coachName" label="姓名" align="center" fixed="left"></el-table-column>
          <el-table-column prop="familyName" label="英文姓" align="center"></el-table-column>
          <el-table-column prop="firstName" label="英文名" align="center"></el-table-column>
          <el-table-column prop="phone" label="手机号" align="center" width="120px"></el-table-column>
          <el-table-column prop="email" label="邮箱地址" align="center" width="200px"></el-table-column>
          <el-table-column prop="sex" label="性别" align="center"></el-table-column>
          <el-table-column prop="size" label="衣服尺寸" align="center"></el-table-column>
          <el-table-column prop="checkStatus" label="审核状态" width="90px" align="center" filter-placement="bottom-end"
                           :filters="[{ text: '已通过', value: 1 }, { text: '待审核', value: 2 },
                       { text: '已驳回', value: 3 }]" :filter-method="filterTag" fixed="right">
            <template slot-scope="scope">
              <el-tag :type="getType(scope.row.checkStatus)" disable-transitions>{{getTag(scope.row.checkStatus)}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="state" label="操作" align="center" width="150px" fixed="right">
            <template slot-scope="scope">
              <el-button type="success" @click="passCoach(scope.row)">通过</el-button>
              <el-button type="danger" @click="rejectCoach(scope.row)">驳回</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[20, 50, 100]"
                       :page-size="pagesize" :layout="layout" :total="c_coachList.length" small="small" pager-count="7"
                       style="padding: 15px 0px 30px 25px">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return{
      schoolId: '',
      coachList: [],
      show: false,
      c_coachList: [],
      checkedCoachList: [],
      searchStr: '',
      currentPage: 1,
      pagesize: '20',
      small: false,
      layout: 'total, sizes, prev, pager, next, jumper'
    }
  },
  created () {
    this.schoolId = this.$store.getters.schoolId
    this.getCoachInfor()
    this.setPage()
  },
  methods: {
    setPage() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.small = true
        this.layout = 'sizes, prev, pager, next'
      } else {
        this.small = false
        this.layout = 'total, sizes, prev, pager, next, jumper'
      }
    },
    handleSizeChange(val) {
      this.pagesize = val
    },
    handleCurrentChange(val) {
      this.currentPage = val
    },
    filterTag(value, row) {
      return row.state === value
    },
    filterType(value, row, column) {
      const prop = column.prop
      return row[prop] === value
    },
    async getCoachInfor() {
      const { data: res } = await this.$http.get('coach/coaches', { params: { schoolId: this.schoolId } })
      if(res.status === 605) {
        this.coachList = res.data
        this.c_coachList = res.data
      }
    },
    getType(checkStatus) {
      if(checkStatus === 1) return 'success'
      else if (checkStatus === 2) return 'primary'
      else return 'danger'
    },
    getTag(checkStatus) {
      if(checkStatus === 1) return '已通过'
      else if (checkStatus === 2) return '待审核'
      else return '已驳回'
    },
    handleSelectionChange(val) {
      this.checkedCoachList = val
    },
    passCoach(coach) {
      var params = coach.id
      this.pass(params, coach)
    },
    rejectCoach(coach) {
      var params = coach.id
      this.reject(params, coach)
    },
    passCoachList() {
      var param = []
      for (var i = 0; i < this.checkedCoachList.length; i++) {
        param.push(this.checkedCoachList[i].id)
      }
      var params = param + ''
      var coach = ''
      this.pass(params, coach)
    },
    rejectCoachList() {
      var param = []
      for (var i = 0; i < this.checkedCoachList.length; i++) {
        param.push(this.checkedCoachList[i].id)
      }
      var params = param + ''
      var coach = ''
      this.reject(params, coach)
    },
    async pass(params, coach) {
      const { data: res } = await this.$http.post('coach/passCoach', this.$qs.stringify({ coachIds: params }))
      if(res.status === 600) {
        this.$message.success('审核成功')
        if(coach === '') this.getCoachINfor()
        else coach.checkStatus = 1
      }else{
        this.$message.error('审核失败')
      }
    },
    async reject(params, coach) {
      const { data: res } = await this.$http.post('coach/rejectCoach', this.$qs.stringify({ coachIds: params }))
      if(res.status === 600) {
        this.$message.success('审核成功')
        if(coach === '') this.getCoachINfor()
        else coach.checkStatus = 3
      }else{
        this.$message.error('审核失败')
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.c_coachList = []
          for(var i in this.coachList) {
            if(JSON.stringify(this.coachList[i]).search(this.searchStr) >= 0) {
              this.c_coachList.push(this.coachList[i])
            }
          }
        } else this.c_coachList = this.coachList
        return this.c_coachList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>
<style>
  tableHead {
    font-size: 10px;
  }
</style>
