<template>
  <div class="findpwd_container">
    <div class="findpwd_box">
      <div class="findpwd_steps">
        <el-steps direction="vertical" :active="active" finish-status="success" process-status="finish" :space="40">
          <el-step icon="el-icon-edit" title="请填写验证邮箱">
            <template slot="description">
              <div id="1">
                <div class="step-row" style="margin-bottom: 30px">
                  <el-form ref="emailFormRef" :model="user" :rules="findFormRules" class="findpwd_form">
                    <el-form-item prop="email">
                      <el-input v-model="user.email" placeholder="邮箱"></el-input>
                    </el-form-item>
                  </el-form>
                </div>
                <el-button class="next-button" type="primary" @click="send(1)" plain>发送验证码</el-button>
              </div>
            </template>
          </el-step>
          <el-step icon="el-icon-upload" title="请填写验证码">
            <template slot="description">
              <div id="2" style="display: none">
                <div class="step-row">
                  <el-form ref="codeFormRef" :model="user" :rules="findFormRules" class="findpwd_form">
                    <el-form-item prop="code">
                      <el-input v-model="user.code" placeholder="验证码" ></el-input>
                    </el-form-item>
                  </el-form>
                </div>
                <el-button class="next-button" type="primary" @click="next(2)" plain>下一步</el-button>
                <el-button class="back-button" type="primary" @click="back(2)" plain>未收到验证码</el-button>
              </div>
            </template>
          </el-step>
          <el-step icon="el-icon-picture" title="请填写新密码">
            <template slot="description">
              <div id="3" style="display: none">
                <div class="step-row">
                  <el-form ref="passwordFormRef" :model="user" :rules="findFormRules" class="findpwd_form">
                    <el-form-item prop="password">
                      <el-input v-model="user.password" placeholder="新密码" type="password" show-password></el-input>
                    </el-form-item>
                    <el-form-item prop="checkPassword">
                      <el-input v-model="user.checkPassword" placeholder="确认密码" type="password" show-password></el-input>
                    </el-form-item>
                  </el-form>
                </div>
                <el-button class="submit-button" type="primary" @click="submit" plain>提交</el-button>
                <el-button class="back-button" type="primary" @click="back(3)" plain>返回</el-button>
              </div>
            </template>
          </el-step>
        </el-steps>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.user.checkPassword !== '') {
          this.$refs.passwordFormRef.validateField('checkPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请确认密码'))
      } else if (value !== this.user.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      active: 0,
      user: {
        email: '',
        code: '123456',
        password: '123456',
        checkPassword: '123456'
      },
      findFormRules: {
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPassword: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ],
        code: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 0, max: 15, message: '请输入正确的验证码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    send(id) {
      this.$refs.emailFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('user/retrieve/code', this.$qs.stringify(this.user.email))
        if (res.status !== 605) {
          this.$message.error('发送验证码失败')
        } else {
          this.$message.success('发送验证码成功')
          var lastDiv = document.getElementById(id)
          lastDiv.style.display = 'none'
          id++
          var nextDiv = document.getElementById(id)
          nextDiv.style.display = 'block'
          if (this.active++ > 3) {
            this.active = 0
          }
        }
      })
    },
    next(id) {
      this.$refs.codeFormRef.validate(valid => {
        if (valid) {
          var lastDiv = document.getElementById(id)
          lastDiv.style.display = 'none'
          id++
          var nextDiv = document.getElementById(id)
          nextDiv.style.display = 'block'
          if (this.active++ > 3) {
            this.active = 0
          }
        }
      })
    },
    back(id) {
      var nextDiv = document.getElementById(id)
      nextDiv.style.display = 'none'
      id--
      var lastDiv = document.getElementById(id)
      lastDiv.style.display = 'block'
      this.active--
    },
    submit() {
      this.$refs.passwordFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('user/retrieve/pswd',
          this.$qs.stringify({
            email: this.user.email,
            code: this.user.code,
            password: this.user.password
          }))
        if (res.status !== 600) return this.$message.error('找回失败')
        else {
          this.$message.success('找回成功')
          this.$router.push('/login')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .findpwd_container{
    height: 100%;
  }
  @media screen and (max-width:480px) {
    .findpwd_box{
      width: 300px;
    }
  }
  @media screen and (min-width:481px) {
    .findpwd_box{
      width: 400px;
    }
  }
  .findpwd_box{
    height: auto;
    height: auto;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    left: 50%;
    transform: translate(-50%,0);
  }
  .findpwd_steps{
    margin: 30px;
  }
  .step-row{
    margin-top: 20px;
  }
  .next-button{
    margin-bottom: 20px;
  }
  .findpwd_form {
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .el-form{
    padding:0;
    margin:0;
    line-height: 45px;
  }
</style>
