<template>
  <div class="container">
    <div class="box">
      <div class="title">
        <span>团队赛管理</span>
        <div>
          <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                    @input="search" clearable="true"></el-input>
        </div>
      </div>
      <div class="shop">
        <el-button  @click="signUp" plain>报名参赛</el-button>
        <el-button  @click="lookSignUpTeamlist" plain>查看参赛队伍</el-button>
        <el-button  @click="manageDinner" plain>晚宴人数设置</el-button>
        <el-button  @click="deleteTeam" plain>队伍管理</el-button>
        <el-button  @click="getAwardList" plain>查看证书</el-button>
      </div>
      <div class="contests">
        <el-checkbox-group v-model="checkList" :max="1">
          <el-row :gutter="20" >
            <el-col :xs="24" :sm="6" v-for="(item, index) in c_contestList" :key="index"><div class="contest">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <el-checkbox :label = "item.id">{{item.contestTitle}}</el-checkbox>
                </div>
                <div  class="information">
                  <el-row >
                    <el-col :span="7">
                      <span style="float: left;">比赛名称</span>
                    </el-col>
                    <el-col :span="17">
                      <span class="infor">{{item.contestTitle}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">开始时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.startTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">结束时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.endTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">报名开始时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.signStartTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">报名结束时间</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{formatDate(item.signEndTime)}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">负责人</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.chief}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">电话</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.phone}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">邮箱</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.email}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">当前已报名队伍数</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.numTeamFact}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">可报名队伍数</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.numTeamMax}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">允许报名队伍总计</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.numTeamTotal}}</span>
                    </el-col>
                  </el-row>
                </div>
              </el-card>
            </div></el-col>
          </el-row>
        </el-checkbox-group>
      </div>
    </div>
    <el-dialog title="报名参赛" :visible.sync="addDialogVisible" :width="dialogWidth" :before-close="handleClose" center="true">
      <el-form ref="signUpContestFormRef" :model="contestSignUpTeam" :rules="addContestFormRules" class="add_Contest_form" label-position="top">
        <el-form-item prop="teamId">
          <el-select v-model="contestSignUpTeam.teamId" filterable placeholder="请选择参赛队伍">
            <el-option v-for="item in teamlist" :key="item.id" :label="item.teamName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="请选择参赛队伍类型">
          <el-radio v-model="contestSignUpTeam.isStarred" label="1">打星参赛</el-radio>
          <el-radio v-model="contestSignUpTeam.isStarred" label="0">正常参赛</el-radio>
        </el-form-item>
        <el-form-item label="请选择教练是否到场">
          <el-radio v-model="contestSignUpTeam.onSite" label="1">是</el-radio>
          <el-radio v-model="contestSignUpTeam.onSite" label="0">否</el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="saveQuota()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="参赛队伍信息" :visible.sync="lookDialogVisible" center width="90%">
      <el-table :data="signUpTeamlist" fit>
        <el-table-column prop="teamName" label="队伍名称" ></el-table-column>
        <el-table-column prop="foreignName" label="队伍英文名称" width="120px"></el-table-column>
        <el-table-column prop="打星" label="队伍类型" >
          {{false? '正常参赛': '打星参赛' }}
        </el-table-column>
        <el-table-column prop="coachName" label="教练姓名"></el-table-column>
        <el-table-column prop="stuAName" label="队员姓名(队长)" width="120px"></el-table-column>
        <el-table-column prop="stuBName" label="队员姓名" ></el-table-column>
        <el-table-column prop="stuCName" label="队员姓名" ></el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog title="参加晚宴人数" :visible.sync="manageDialogVisible" center :width="dialogWidth" :before-close="handleClose">
      <p>正式赛后会组织教练晚宴，请表示参加晚宴的教练数，数目不能超过报名比赛队伍数</p>
      <el-form ref="setNumFormRef" :model="contestDinnerNum" :rules="addContestFormRules" class="add_Contest_form" label-position="top">
        <el-form-item prop="num">
          <el-input v-model="contestDinnerNum.num" placeholder="进餐人数"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="saveNum()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="删除队伍" :visible.sync="deleteDialogVisible" center :width="dialogWidth" :before-close="handleClose">
      <el-form ref="cancelContestFormRef" :model="contestDeleteTeam" :rules="addContestFormRules" class="add_Contest_form" label-position="top">
        <el-form-item prop="deleteTeamId">
          <el-select v-model="contestDeleteTeam.teamId" filterable placeholder="请选择要删除的参赛队伍">
            <el-option v-for="item in signUpTeamlist" :key="item.id" :label="item.teamName" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="saveDelete()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="证书列表" :visible.sync="awardDialogVisible" :width="awardDialogWidth">
      <el-button  @click="downLoad" plain>批量下载</el-button>
      <el-table :data="awardList" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label="证书名称" ></el-table-column>
        <el-table-column prop="state" label="操作" width="100px" align="center">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="预览" placement="top">
              <el-button type="primary" icon="el-icon-view" @click="viewNews(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addDialogVisible: false,
      lookDialogVisible: false,
      manageDialogVisible: false,
      deleteDialogVisible: false,
      awardDialogVisible: false,
      awardDialogWidth: 0,
      dialogWidth: 0,
      tablewidth: 0,
      awardList: [],
      checkedAwardList: [],
      checkList: [],
      contestList: [],
      c_contestList: [],
      teamlist: [],
      signUpTeamlist: [],
      searchStr: '',
      schoolId: '',
      schools: [],
      contestDeleteTeam: {
        contestId: '',
        teamId: ''
      },
      contestSignUpTeam: {
        contestId: '',
        isStarred: '0',
        onSite: '0',
        teamId: ''
      },
      contestDinnerNum: {
        contestId: '',
        schoolId: '',
        num: ''
      },
      contestTeamList: [],
      addContestFormRules: {
        teamId: [
          { required: true, message: '请选择队伍', trigger: 'blur' }
        ],
        num: [
          { required: true, message: '请填写晚宴人数', trigger: 'blur' },
          { type: 'number', message: '请填写正确的晚宴人数', trigger: 'blur', transform: (value) => Number(value) }
        ],
        deleteTeamId: [
          { required: true, message: '请选择队伍', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.schoolId = this.$store.getters.schoolId
    this.contestDinnerNum.schoolId = this.$store.getters.schoolId
    this.setDialogWidth()
    this.getContestList()
    this.getTeamlist()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setDialogWidth()
      })()
    }
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '80%'
        this.tablewidth = 150
        this.awardDialogWidth = '90%'
      } else {
        this.dialogWidth = '360px'
        this.tablewidth = 200
        this.awardDialogWidth = '70%'
      }
    },
    async getContestList() {
      const { data: res } = await this.$http.get('coach/contest', { params: { schoolId: this.schoolId } })
      if(res.status === 605) {
        this.contestList = res.data
        this.c_contestList = res.data
      }
    },
    async getTeamlist () {
      const { data: res } = await this.$http.get('coach/team', { params: { schoolId: this.schoolId } })
      if (res.status === 605) {
        this.teamlist = res.data
      }
    },
    lookSignUpTeamlist() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        this.lookDialogVisible = true
        this.getSignUpTeamlist()
      }
    },
    async getSignUpTeamlist () {
      const { data: res } = await this.$http.get('coach/teamContest',
        { params: { contestId: this.contestDeleteTeam.contestId = this.checkList[0] } })
      if (res.status === 605) {
        this.signUpTeamlist = res.data
      }
    },
    async getAwardList() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        const { data: res } = await this.$http.get('contest/credential/info', { params: { contestId: this.checkList[0] } })
        if (res.status === 605) {
          this.awardList = res.data
        }
        this.awardDialogVisible = true
      }
    },
    formatDate (date) {
      date = new Date(date)
      var w = date.getDay()
      if(w === 0) w = 'Sun'
      else if (w === 1) w = 'Mon'
      else if (w === 2) w = 'Tues'
      else if (w === 3) w = 'Wed'
      else if (w === 4) w = 'Thur'
      else if (w === 5) w = 'Fri'
      else w = 'Sat'
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return w + ' ' + y + '-' + m + '-' + d
    },
    saveDelete() {
      this.$confirm('确定删除该队伍？')
        .then(_ => {
          this.Delete()
        })
        .catch(_ => {})
    },
    saveNum() {
      this.$confirm('确定保存？')
        .then(_ => {
          this.setNum()
        })
        .catch(_ => {})
    },
    saveQuota() {
      this.$confirm('确定该队伍报名？')
        .then(_ => {
          this.addTeam()
        })
        .catch(_ => {})
    },
    cancel() {
      this.$confirm('确定取消添加或更改？')
        .then(_ => {
          this.resetFields()
        })
        .catch(_ => {})
    },
    handleClose(done) {
      this.$confirm('信息未提交，确定关闭？')
        .then(_ => {
          this.resetFields()
        })
        .catch(_ => {})
    },
    resetFields() {
      if(this.addDialogVisible === true) {
        this.addDialogVisible = false
        this.$refs.signUpContestFormRef.resetFields()
      } else if(this.deleteDialogVisible === true) {
        this.deleteDialogVisible = false
        this.$refs.cancelContestFormRef.resetFields()
      } else {
        this.manageDialogVisible = false
        this.$refs.setNumFormRef.resetFields()
      }
    },
    signUp() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else this.addDialogVisible = true
    },
    addTeam() {
      this.contestSignUpTeam.contestId = this.checkList[0]
      this.$refs.signUpContestFormRef.validate(async valid => {
        if(!valid) return
        const { data: res } = await this.$http.post('coach/signUp', this.$qs.stringify(this.contestSignUpTeam))
        if(res === 600) {
          this.$message.success('添加成功')
          // this.getContestList()
        }else if(res.status === 730) {
          this.$message.error('队伍不存在')
        }else if(res.status === 732) {
          this.$message.error('名额不足')
        } else if(res.status === 731) {
          this.$message.error('团队赛不存在')
        } else this.$message.error('添加失败')
      })
    },
    manageDinner() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else this.manageDialogVisible = true
    },
    setNum() {
      this.contestDinnerNum.contestId = this.checkList[0]
      this.$refs.setNumFormRef.validate(async valid => {
        if(!valid) return
        const { data: res } = await this.$http.post('coach/contest/meal', this.$qs.stringify(this.contestDinnerNum))
        if(res === 600) {
          this.$message.success('添加成功')
          // this.getContestList()
        }
        this.$message.error('添加失败')
      })
    },
    deleteTeam() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        this.getSignUpTeamlist()
        this.deleteDialogVisible = true
      }
    },
    Delete() {
      this.contestDeleteTeam.contestId = this.checkList[0]
      this.$refs.cancelContestFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('coach/contest/quit', this.$qs.stringify(this.contestDeleteTeam))
        if (res.status === 600) {
          this.$message.success('删除成功')
          this.deleteDialogVisible = false
        } else if(res.status === 730) {
          this.$message.error('队伍不存在')
        }else if(res.status === 731) {
          this.$message.error('团队赛不存在')
        } else this.$message.error('删除失败')
      })
    },
    async viewNews(id) {
      const { data: res } = await this.$http.get('contest/credential', { params: { id: id } })
      const link = document.createElement('a')
      link.href = res.data
      link.download = '' // 重命名文件
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    },
    handleSelectionChange(val) {
      this.checkedAwardList = val
    },
    async downLoad() {
      if(this.checkedAwardList.length === 0) this.$message.error('请选择要下载的奖状')
      else {
        var params = []
        for (var i = 0; i < this.checkedAwardList.length; i++) {
          params.push(this.checkedAwardList[i].id)
        }
        var a = 'http://47.100.89.70:10010/api/contest/credentials?ids=' + params
        const link = document.createElement('a')
        link.href = a
        link.download = '' // 重命名文件
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.c_contestList = []
          for(var i in this.contestList) {
            if(JSON.stringify(this.contestList[i]).search(this.searchStr) >= 0) {
              this.c_contestList.push(this.contestList[i])
            }
          }
        } else this.c_contestList = this.contestList
        return this.c_contestList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }

  }
}
</script>

