      <el-button  @click="downLoad" plain>批量下载</el-button>
      <el-table :data="awardList" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label="证书名称" ></el-table-column>
        <el-table-column prop="state" label="操作" width="100px" align="center">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="预览" placement="top">
              <el-button type="primary" icon="el-icon-view" @click="viewNews(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
     </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      lookDialogVisible: false,
      awardDialogVisible: false,
      awardDialogWidth: 0,
      checkedAwardList: [],
      tablewidth: 0,
      checkList: [],
      awardList: [],
      contestList: [],
      c_contestList: [],
      signUpTeamlist: [],
      searchStr: ''
    }
  },
  created() {
    this.schoolId = this.$store.getters.schoolId
    this.getContestList()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setDialogWidth()
      })()
    }
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.tablewidth = 150
        this.awardDialogWidth = '90%'
      } else {
        this.tablewidth = '100%'
        this.awardDialogWidth = '70%'
      }
    },
    async getContestList() {
      const { data: res } = await this.$http.get('solo')
      if (res.status === 605) {
        this.contestList = res.data
        this.c_contestList = res.data
      }
    },
    async getSignUpTeamlist () {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        this.lookDialogVisible = true
        const { data: res } = await this.$http.get('coach/solo/schoolStatus', { params: { soloContestId: this.checkList[0] } })
        if (res.status === 605) {
          this.signUpTeamlist = res.data
        }
      }
    },
    async getAwardList() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个比赛')
      } else {
        const { data: res } = await this.$http.get('solo/credential/info', { params: { soloContestId: this.checkList[0] } })
        if (res.status === 605) {
          this.awardList = res.data
        }
        this.awardDialogVisible = true
      }
    },
    formatDate (date) {
      date = new Date(date)
      var w = date.getDay()
      if(w === 0) w = 'Sun'
      else if (w === 1) w = 'Mon'
      else if (w === 2) w = 'Tues'
      else if (w === 3) w = 'Wed'
      else if (w === 4) w = 'Thur'
      else if (w === 5) w = 'Fri'
      else w = 'Sat'
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return w + ' ' + y + '-' + m + '-' + d
    },
    async viewNews(id) {
      const { data: res } = await this.$http.get('solo/credential', { params: { id: id } })
      const link = document.createElement('a')
      link.href = res.data
      link.download = '' // 重命名文件
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    },
    handleSelectionChange(val) {
      this.checkedAwardList = val
    },
    async downLoad() {
      if(this.checkedAwardList.length === 0) this.$message.error('请选择要下载的奖状')
      else {
        var params = []
        for (var i = 0; i < this.checkedAwardList.length; i++) {
          params.push(this.checkedAwardList[i].id)
        }
        var a = 'http://47.100.89.70:10010/api/solo/credentials?ids=' + params
        const link = document.createElement('a')
        link.href = a
        link.download = '' // 重命名文件
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.c_contestList = []
          for(var i in this.contestList) {
            if(JSON.stringify(this.contestList[i]).search(this.searchStr) >= 0) {
              this.c_contestList.push(this.contestList[i])
            }
          }
        } else this.c_contestList = this.contestList
        return this.c_contestList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }

  }
}
</script>

