<template>
  <div class="container">
    <div class="box">
      <div class="title">
        <span>发票管理</span>
        <div>
          <el-input placeholder="请输入内容" v-model="searchStr" class="input-with-select" suffix-icon="el-icon-search"
                    @input="search" clearable="true"></el-input>
        </div>
      </div>
      <div class="shop">
        <el-button  @click="beforeDialogOpen" plain>开发票/修改发票</el-button>
      </div>
      <div class="bills">
        <el-checkbox-group v-model="checkList" :max="1">
            <el-row :gutter="20" >
            <el-col :xs="24" :sm="6" v-for="(item, index) in b_billList" :key="index"><div class="bill">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <el-checkbox :label = "item">{{item.billEnterprise}}</el-checkbox>
                </div>
                <div  class="information">
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">发票单位</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.billEnterprise}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">税号</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.taxNum}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">单位地址</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.address}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">单位电话</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.telephone}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">开户银行</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.bank}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">银行账户</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.bankAccount}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">发票内容</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.content}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">发票类型</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.type}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">金额</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.money}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="10">
                      <span style="float: left;">是否可更改</span>
                    </el-col>
                    <el-col :span="14">
                      <span class="infor">{{item.checked === 2? '可以修改' : '发票已经开出，不能修改'}}</span>
                    </el-col>
                  </el-row>
                </div>
              </el-card>
            </div></el-col>
          </el-row>
        </el-checkbox-group>
      </div>
    </div>
    <el-dialog title="添加或修改发票" :visible.sync="addDialogVisible" :width="dialogWidth" :before-close="handleClose" @opened="open">
      <el-form ref="VATFormRef" :model="bill" :rules="addbillFormRules" class="add_bill_form" label-position="top">
        <el-form-item label="发票类型">
          <el-radio v-model="bill.type" label="增值税发票" @change="isFold(0)">增值税发票</el-radio>
          <el-radio v-model="bill.type" label="普通发票" @change="isFold(1)">普通发票</el-radio>
        </el-form-item>
        <el-form-item label="发票内容" prop="content">
          <el-select v-model="bill.content" filterable placeholder="发票内容">
            <el-option
              v-for="item in contents" :key="item.value" :label="item.label" :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="发票单位" prop="billEnterprise">
          <el-input v-model="bill.billEnterprise" placeholder="发票单位"></el-input>
        </el-form-item>
        <el-form-item label="税号" prop="taxNum">
          <el-input v-model="bill.taxNum" placeholder="税号"></el-input>
        </el-form-item>
        <el-form-item label="金额" prop="money">
          <el-input v-model="bill.money" placeholder="金额"></el-input>
        </el-form-item>
      </el-form>
      <div id="isGeneral">
        <el-form ref="plainBillFormRef" :model="bill" :rules="addbillFormRules" class="add_bill_form" label-position="top">
          <el-form-item label="单位地址" prop="address">
            <el-input v-model="bill.address" placeholder="单位地址"></el-input>
          </el-form-item>
          <el-form-item label="开户银行" prop="bank">
            <el-input v-model="bill.bank" placeholder="开户银行"></el-input>
          </el-form-item>
          <el-form-item label="银行账户" prop="bankAccount">
            <el-input v-model="bill.bankAccount" placeholder="银行账户"></el-input>
          </el-form-item>
          <el-form-item label="单位电话" prop="telephone">
            <el-input v-model="bill.telephone" placeholder="单位电话"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    var validatePhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入单位电话'))
      } else {
        const reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/
        const reg2 = /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/
        if (reg.test(value) || reg2.test(value)) {
          callback()
        } else {
          return callback(new Error('请输入正确的单位电话'))
        }
      }
    }
    return {
      contents: [
        {
          value: 0,
          label: '参赛费'
        },
        {
          value: 1,
          label: '会议费'
        },
        {
          value: 2,
          label: '会议注册费'
        }
      ],
      addDialogVisible: false,
      dialogWidth: 0,
      checkList: [],
      billList: [],
      b_billList: [],
      searchStr: '',
      bill: {
        id: '',
        schoolId: '',
        billEnterprise: '',
        taxNum: '',
        address: '',
        telephone: '',
        bank: '',
        bankAccount: '',
        content: '',
        type: '增值税发票',
        money: '',
        checked: ''
      },
      addbillFormRules: {
        content: [
          { required: true, message: '请选择发票内容', trigger: 'change' }
        ],
        billEnterprise: [
          { required: true, message: '请输入发票单位', trigger: 'blur' }
        ],
        telephone: [
          { required: true, validator: validatePhone, trigger: 'blur' }
        ],
        bank: [
          { required: true, message: '请输入开户银行', trigger: 'blur' }
        ],
        taxNum: [
          { required: true, message: '请输入税号', trigger: 'blur' }
        ],
        money: [
          { required: true, message: '请输入金额', trigger: 'blur' },
          { type: 'number', message: '请输入正确的金额', trigger: 'blur', transform: (value) => Number(value) }
        ],
        address: [
          { required: true, message: '请输入单位地址', trigger: 'blur' }
        ],
        bankAccount: [
          { required: true, message: '请输入银行账户', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.bill.schoolId = this.$store.getters.schoolId
    this.setDialogWidth()
    this.getBillList()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setDialogWidth()
      })()
    }
  },
  methods: {
    setDialogWidth() {
      var val = document.body.clientWidth
      const def = 450 // 默认宽度
      if (val < def) {
        this.dialogWidth = '80%'
      } else {
        this.dialogWidth = '360px'
      }
    },
    async getBillList() {
      const { data: res } = await this.$http.get('coach/bill', { params: { schoolId: this.bill.schoolId } })
      if(res.status === 605) {
        this.billList = res.data
        this.b_billList = res.data
      }
    },
    save() {
      this.$confirm('确认保存当前添加/修改？')
        .then(_ => {
          this.billValidate()
        })
        .catch(_ => {})
    },
    cancel() {
      this.$confirm('确认放弃添加/修改？')
        .then(_ => {
          this.addDialogVisible = false
          this.$refs.VATFormRef.resetFields()
          this.$refs.plainBillFormRef.resetFields()
          // this.isFold(0)
        })
        .catch(_ => {})
    },
    handleClose(done) {
      this.$confirm('确认放弃添加/修改？')
        .then(_ => {
          done()
          this.$refs.VATFormRef.resetFields()
          this.$refs.plainBillFormRef.resetFields()
          // this.isFold(0)
        })
        .catch(_ => {})
    },
    beforeDialogOpen() {
      if(this.checkList.length !== 0) {
        if(this.checkList[0].checked === 1) this.$message.error('当前发票已开出，不可修改')
        else {
          this.bill = Object.assign({}, this.checkList[0])
          this.addDialogVisible = true
        }
      }else {
        for(var key in this.bill) {
          this.bill[key] = ''
        }
        this.bill.type = '增值税发票'
        this.bill.schoolId = this.$store.getters.schoolId
        this.addDialogVisible = true
      }
    },
    open() {
      var val = 1
      if(this.bill.type === '普通发票') val = 1
      else val = 0
      this.isFold(val)
    },
    async billValidate() {
      const p1 = new Promise((resolve, reject) => {
        this.$refs.VATFormRef.validate(valid => {
          if(valid) resolve()
        })
      })
      const p2 = new Promise((resolve, reject) => {
        this.$refs.plainBillFormRef.validate(valid => {
          if(valid) resolve()
        })
      })
      var _this = this
      var params = {
        schoolId: this.bill.schoolId,
        billEnterprise: this.bill.billEnterprise,
        taxNum: this.bill.taxNum,
        address: this.bill.address,
        telephone: this.bill.telephone,
        bank: this.bill.bank,
        bankAccount: this.bill.bankAccount,
        content: this.bill.content,
        type: this.bill.type,
        money: this.bill.money
      }
      if(this.bill.type === '普通发票') {
        Promise.all([p1]).then(async function() {
          if(_this.checkList.length === 0) {
            _this.addBill(params)
          }else _this.changeBill()
        })
      }else {
        Promise.all([p1, p2]).then(async function() {
          if(_this.checkList.length === 0) {
            _this.addBill(params)
          }else _this.changeBill()
        })
      }
    },
    async addBill(params) {
      const { data: res } = await this.$http.post('coach/bill', this.$qs.stringify(params))
      if(res.status === 600) {
        this.$message.success('添加成功')
        // this.bill.type = '增值税发票'
        // this.isFold(0)
        this.checkList = []
        this.$refs.VATFormRef.resetFields()
        this.$refs.plainBillFormRef.resetFields()
        this.addDialogVisible = false
        this.getBillList()
      }else this.$message.error('添加失败')
    },
    async changeBill() {
      const { data: res } = await this.$http.post('coach/bill/update', this.$qs.stringify(this.bill))
      if(res.status === 600) {
        this.$message.success('修改成功')
        this.checkList = []
        this.$refs.VATFormRef.resetFields()
        this.$refs.plainBillFormRef.resetFields()
        this.addDialogVisible = false
        this.getBillList()
      }else this.$message.error('修改失败')
    },
    isFold(val) {
      var isGeneral = document.getElementById('isGeneral')
      if(val === 1) {
        isGeneral.style.display = 'none'
      } else isGeneral.style.display = 'block'
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.b_billList = []
          for(var i in this.billList) {
            if(JSON.stringify(this.billList[i]).search(this.searchStr) >= 0) {
              this.b_billList.push(this.billList[i])
            }
          }
        } else this.b_billList = this.billList
        return this.b_billList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>
