        <el-checkbox-group v-model="checkList">
          <!--        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>-->
          <el-row :gutter="20" >
            <el-col :xs="24" :sm="6" v-for="(item, index) in b_billList" :key="index"><div class="bill">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <el-checkbox :label = "item.id">{{item.billEnterprise}}</el-checkbox>
                </div>
                <div  class="information">
                  <el-row >
                    <el-col :span="6">
                      <span style="float: left;">发票单位</span>
                    </el-col>
                    <el-col :span="18">
                      <span class="infor">{{item.billEnterprise}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">税号</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.taxNum}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">单位地址</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.address}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">单位电话</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.telephone}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">开户银行</span>
                    </el-col>
                    <el-col :span="12">
                      <span class="infor">{{item.bank}}</span>
                    </el-col>
                  </el-row>
                  <el-row >
                    <el-col :span="12">
                      <span style="float: left;">银行账户</span>
                    </el-col>
                    <el-col :span="12">
