                  </el-row>
                  <el-row >
                    <el-col :span="10">
                      <span style="float: left;">是否开具</span>
                    </el-col>
                    <el-col :span="14">
                      <span class="infor">{{item.checked === 2? '未开具' : '已开具'}}</span>
