                </div>
              </el-card>
            </div></el-col>
          </el-row>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      billList: [],
      checkList: [],
      b_billList: [],
      searchStr: ''
    }
  },
  created () {
    this.getBillList()
  },
  methods: {
    async getBillList() {
      const { data: res } = await this.$http.get('admin/bill')
      if(res.status === 605) {
        this.billList = res.data
        this.b_billList = res.data
      }
    },
    async exportBills() {
      if(this.checkList.length === 0) {
        this.$message.error('请选择一个发票')
      } else {
        const { data: res } = await this.$http.post('admin/bill/check', this.$qs.stringify({ billIds: this.checkList + '' }))
        if (res.status === 600) {
          this.$message.success('导出成功')
          this.getBillList()
        } else this.$message.error('导出失败')
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.b_billList = []
          for(var i in this.billList) {
            if(JSON.stringify(this.billList[i]).search(this.searchStr) >= 0) {
              this.b_billList.push(this.billList[i])
            }
          }
        } else this.b_billList = this.billList
        return this.b_billList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

