<template>
  <div class="register_container">
    <div class="register_box">
      <div class="register_steps">
        <el-steps direction="vertical" :active="active" finish-status="success" process-status="finish" :space="40">
          <el-step icon="el-icon-edit" title="请选择身份">
            <template slot="description">
              <div id="1">
                <div class="step-row" style="margin-bottom: 30px">
                  <el-radio-group v-model="radio">
                    <el-radio :label="3">学生</el-radio>
                    <el-radio :label="6">教练</el-radio>
                    <el-radio :label="9">学校(包含教练)</el-radio>
                  </el-radio-group>
                </div>
                <el-button class="next-button" type="primary" @click="next(1,2)" plain>下一步</el-button>
              </div>
            </template>
          </el-step>
          <el-step icon="el-icon-upload" title="请填写基本信息">
            <template slot="description">
              <div id="2" style="display: none">
                <div class="step-row">
                  <el-form ref="registerBaseFormRef" :model="user" :rules="registerFormRules" class="register_form">
                    <el-form-item prop="phone">
                      <el-input v-model="user.phone" placeholder="手机号"></el-input>
                    </el-form-item>
                    <el-form-item prop="stuName">
                      <el-input v-model="user.stuName" placeholder="中文姓名"></el-input>
                    </el-form-item>
                    <el-form-item prop="firstName">
                      <el-input v-model="user.firstName" placeholder="英文名"></el-input>
                    </el-form-item>
                    <el-form-item prop="familyName">
                      <el-input v-model="user.familyName" placeholder="英文姓"></el-input>
                    </el-form-item>
                    <el-form-item prop="email">
                      <el-input v-model="user.email" placeholder="邮箱"></el-input>
                    </el-form-item>
                    <el-form-item prop="pswd">
                      <el-input v-model="user.pswd" placeholder="密码" type="password" show-password></el-input>
                    </el-form-item>
                  </el-form>
                </div>
                <el-button class="next-button" type="primary" @click="next(2, 3,radio)" plain>下一步</el-button>
                <el-button class="back-button" type="primary" @click="back(2,1)" plain>返回</el-button>
              </div>
            </template>
          </el-step>
          <el-step icon="el-icon-picture" title="请填写学校信息">
            <template slot="description">
              <div id="3" style="display: none">
                <div class="step-row">
                  <el-form ref="registerStudentFormRef" :model="user" :rules="registerFormRules" class="register_form">
                    <el-form-item prop="stuId">
                      <el-input v-model="user.stuId" placeholder="学号"></el-input>
                    </el-form-item >
                    <el-form-item prop="schoolID">
                      <el-select v-model="user.schoolId" filterable placeholder="学校">
                        <el-option v-for="item in schools" :key="item.id" :label="item.schoolName" :value="item.id">
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item prop="college">
                      <el-input v-model="user.college" placeholder="院系"></el-input>
                    </el-form-item>
                    <el-form-item prop="major">
                      <el-input v-model="user.major" placeholder="专业"></el-input>
                    </el-form-item>
                    <el-form-item prop="enrollYear" >
                      <el-date-picker class="date-picker" v-model="user.enrollYear" type="year" value-format="yyyy"
                                      placeholder="入学年份" ></el-date-picker>
                    </el-form-item>
                  </el-form>
                </div>
                <el-button class="next-button" type="primary" @click="next(3,6)" plain>下一步</el-button>
                <el-button class="back-button" type="primary" @click="back(3,2)" plain>返回</el-button>
              </div>
              <div id="4" style="display: none">
                <div class="step-row">
                  <el-form ref="registerTeacherFormRef" :model="user" :rules="registerFormRules" class="register_form">
                    <el-form-item prop="schoolId">
                      <el-select v-model="user.schoolId" filterable placeholder="学校">
                        <el-option v-for="item in schools" :key="item.id" :label="item.schoolName" :value="item.id">
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-form>
                </div>
                <el-button class="next-button" type="primary" @click="next(4,6)" plain>下一步</el-button>
                <el-button class="back-button" type="primary" @click="back(4, 2)" plain>返回</el-button>
              </div>
              <div id="5" style="display: none">
                <div class="step-row">
                  <el-form ref="registerSchoolFormRef" :model="user" :rules="registerFormRules" class="register_form">
                    <el-form-item prop="schoolName">
                      <el-input v-model="schoolName" placeholder="学校"></el-input>
                    </el-form-item >
                  </el-form>
                </div>
                <el-button class="next-button" type="primary" @click="next(5,6)" plain>下一步</el-button>
                <el-button class="back-button" type="primary" @click="back(5,2)" plain>返回</el-button>
              </div>
            </template>
          </el-step>
          <el-step icon="el-icon-picture" title="队服信息">
            <template slot="description">
              <div id="6" style="display: none">
                <div class="step-row">
                  <el-form ref="registerClothesFormRef" :model="user" :rules="registerFormRules" class="register_form">
                    <el-from-item prop="sex">
                      <el-radio v-model="user.sex" label="男">男</el-radio>
                      <el-radio v-model="user.sex" label="女">女</el-radio>
                    </el-from-item>
                    <el-form-item prop="size" style="margin-top: 20px; padding: 0 40px 0 0;">
                      <el-select v-model="user.size" filterable placeholder="T恤尺码">
                        <el-option
                          v-for="item in sizes" :key="item.value" :label="item.label" :value="item.label">
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-form>
                </div>
                <el-button class="submit-button" type="primary" @click="submit(radio)" plain>提交</el-button>
                <el-button class="back-button" type="primary" @click="back(6,3,radio)" plain>返回</el-button>
              </div>
            </template>
          </el-step>
        </el-steps>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    var validatePhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入手机号'))
      } else {
        const reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/
        if (reg.test(value)) {
          callback()
        } else {
          return callback(new Error('请输入正确的手机号'))
        }
      }
    }
    return {
      radio: 3,
      active: 0,
      schoolName: '上海',
      user: {
        stuId: '',
        stuName: '',
        familyName: '',
        firstName: '',
        schoolId: '',
        college: '',
        major: '',
        enrollYear: '',
        phone: '',
        email: '',
        sex: '',
        size: '',
        pswd: ''
      },
      sizes: [{
        value: 1,
        label: 'XS'
      }, {
        value: 2,
        label: 'S'
      }, {
        value: 3,
        label: 'M'
      }, {
        value: 4,
        label: 'L'
      }, {
        value: 5,
        label: 'XL'
      }, {
        value: 6,
        label: 'XXL'
      }, {
        value: 7,
        label: 'XXXL'
      }
      ],
      schools: [],
      registerFormRules: {
        phone: [
          { required: true, validator: validatePhone, trigger: 'blur' }
        ],
        pswd: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        stuName: [
          { required: true, message: '请输入中文姓名', trigger: 'blur' }
        ],
        firstName: [
          { required: true, message: '请输入英文名', trigger: 'blur' }
        ],
        familyName: [
          { required: true, message: '请输入英文姓', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱', trigger: 'blur' }
        ],
        stuId: [
          { required: true, message: '请输入学号', trigger: 'blur' }
        ],
        enrollYear: [
          { required: true, message: '请输入入学年份', trigger: 'blur' }
        ],
        college: [
          { required: true, message: '请输入学院', trigger: 'blur' }
        ],
        major: [
          { required: true, message: '请输入专业', trigger: 'blur' }
        ],
        schoolId: [
          { required: true, message: '请选择学校', trigger: 'change' }
        ],
        size: [
          { required: true, message: '请选择T恤尺码', trigger: 'change' }
        ],
        schoolName: [
          { required: true, message: '请输入学校名称', trigger: 'change' }
        ]
      }
    }
  },
  // watch: {
  //   'user.phone' (val) {
  //     this.checkPhone(val)
  //   }
  // },
  mounted() {
    this.getSchoolList()
  },
  methods: {
    async submit(radio) {
      var p3 = ''
      // eslint-disable-next-line no-unused-vars
      var name = ''
      // eslint-disable-next-line no-unused-vars
      var params
      const p1 = new Promise((resolve, reject) => {
        this.$refs.registerBaseFormRef.validate(valid => {
          if(valid) resolve()
        })
      })
      const p2 = new Promise((resolve, reject) => {
        this.$refs.registerClothesFormRef.validate(valid => {
          if(valid) resolve()
        })
      })
      if(radio === 3) {
        p3 = new Promise((resolve, reject) => {
          this.$refs.registerStudentFormRef.validate(valid => {
            if(valid) resolve()
          })
        })
        name = 'student'
        params = this.user
      } else if(radio === 6) {
        p3 = new Promise((resolve, reject) => {
          this.$refs.registerTeacherFormRef.validate(valid => {
            if(valid) resolve()
          })
        })
        name = 'coach'
        params = {
          coachName: this.user.stuName,
          familyName: this.user.familyName,
          firstName: this.user.firstName,
          schoolId: this.user.schoolId,
          phone: this.user.phone,
          email: this.user.email,
          sex: this.user.sex,
          size: this.user.size,
          pswd: this.user.pswd
        }
      } else {
        p3 = new Promise((resolve, reject) => {
          this.$refs.registerSchoolFormRef.validate(valid => {
            if(valid) resolve()
          })
        })
        name = 'school'
        params = {
          coachName: this.user.stuName,
          familyName: this.user.familyName,
          firstName: this.user.firstName,
          schoolName: this.schoolName,
          phone: this.user.phone,
          email: this.user.email,
          sex: this.user.sex,
          size: this.user.size,
          pswd: this.user.pswd
        }
      }
      var _this = this
      Promise.all([p1, p2, p3]).then(async function() {
        // const { data: res } = await _this.$http.get('user/exist', { params: { phone: _this.user.phone } })
        const { data: res } = await _this.$http.post('user/' + name, _this.$qs.stringify(params))
        if (res.status === 600) {
          _this.$message.success('注册成功')
          _this.$router.push('/login')
        }else if(res.status === 711) {
          _this.$message.error('用户已存在')
        } else if(res.status === 712) {
          _this.$message.error('学校已注册')
        } else if(res.status === 713) {
          _this.$message.error('学校未注册')
        } else {
          _this.$message.error('注册失败')
        }
      })
    },
    next (lastid, nextid, radio) {
      var lastDiv = document.getElementById(lastid)
      lastDiv.style.display = 'none'
      if(radio !== null) {
        if(radio === 6) nextid++
        else if(radio === 9) nextid = nextid + 2
      }
      var nextDiv = document.getElementById(nextid)
      nextDiv.style.display = 'block'
      if (this.active++ > 3) {
        this.active = 0
      }
    },
    back (nextid, lastid, radio) {
      var nextDiv = document.getElementById(nextid)
      nextDiv.style.display = 'none'
      if(radio !== null) {
        if(radio === 6) lastid++
        else if(radio === 9) lastid = lastid + 2
      }
      var lastDiv = document.getElementById(lastid)
      lastDiv.style.display = 'block'
      this.active--
    },
    async getSchoolList() {
      const { data: res } = await this.$http.get('user/school')
      if(res.status === 605) {
        this.schools = res.data
      }
    }
    // checkPhone(phone) {
    //   setTimeout(async function() {
    //     const { data: res } = await this.$http.get('user/exist', this.$qs.stringify(phone))
    //     // eslint-disable-next-line no-undef
    //     callback(new Error('请确认新密码'))
    //   }, 2000)
    // }
  }
}
</script>

