<template>
  <div class="login_container">
    <div class="login_box">
      <div class="avatar_box">
        <img src="../../assets/logo.jpg" alt="">
      </div>
      <div class="login_title">
        <p>用户登录</p>
      </div>
      <el-form ref="loginFormRef" :model="user" :rules="loginFormRules" class="login_form">
        <el-form-item prop="phone">
          <el-input v-model="user.phone" prefix-icon="el-icon-mobile" placeholder="手机号"></el-input>
        </el-form-item>
        <el-form-item prop="pswd" style="margin-bottom: 5px;">
          <el-input v-model="user.pswd" prefix-icon="el-icon-lock" placeholder="密码" type="password"
                    show-password="true"></el-input>
        </el-form-item>
        <el-form-item style="margin-bottom: 5px; padding: 0 20px;">
<!--          <el-checkbox v-model="checked">记住我</el-checkbox>-->
          <router-link :to="{path:'findPassword'}" class="link">忘记密码？</router-link>
        </el-form-item>
        <el-form-item>
          <el-button class="btns" type="primary" @click="login" round plain>登陆</el-button>
          <el-divider content-position="center">或</el-divider>
          <el-button class="btns" type="primary" @click="register" round plain>注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      checked: '',
      email: 'sdsdada',
      user: {
        phone: '',
        pswd: ''
      },
      student: {
        stuId: '16123056',
        stuName: '梁芷晴',
        familyName: 'liang',
        firstName: 'zhiqing',
        schoolID: 1,
        college: '计算机',
        major: '计算机',
        enrollYear: 2016,
        phone: '198',
        email: '1239550834@qq.com',
        sex: 1,
        size: 4,
        pswd: '123'
      },
      loginFormRules: {
        phone: [
          { required: true, message: '请输入手机号码', trigger: 'blur' },
          { min: 3, max: 15, message: '请输入正确的手机号码', trigger: 'blur' }
        ],
        pswd: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 1, max: 15, message: '长度在 8 到 15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if(!valid) return
        const { data: res } = await this.$http.post('user/login', this.$qs.stringify(this.user))
        if (res.status === 600) {
          this.$message.success('登陆成功')
          this.$store.commit('changeStuId', res.data.id)
          this.$router.push('/student')
        } else if(res.status === 601 || res.status === 602) {
          this.$message.success('登陆成功')
          this.$store.commit('changeCoachId', res.data.id)
          this.$store.commit('changeSchoolId', res.data.schoolId)
          this.$router.push('/coach')
        } else if(res.status === 700) {
          this.$message.error('账号正在审核')
        }else if(res.status === 701) {
          this.$message.error('注册请求被驳回')
        }else if(res.status === 702) {
          this.$message.error('账号不存在')
        }else if(res.status === 703) {
          this.$message.error('密码错误')
        } else{
          this.$message.error('登陆失败')
        }
      })
    },
    register: function() {
      this.$router.push('/register')
    }
  }
}
</script>
