import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    studentId: '' || localStorage.getItem('studentId'),
    coachId: '' || localStorage.getItem('coachId'),
    schoolId: '' || localStorage.getItem('schoolId'),
    adminId: '' || localStorage.getItem('adminId'),
    componentName: '',
    article: {
      id: '',
      title: '',
      intro: '',
      content: '',
      coverUrl: '',
      adminId: '',
      latestEditTime: '',
      tag: ''
    }
  },
  mutations: {
    changeStuId(state, id) {
      state.studentId = id
      localStorage.setItem('studentId', state.studentId)
    },
    changeCoachId(state, id) {
      state.coachId = id
      localStorage.setItem('coachId', state.coachId)
    },
    changeSchoolId(state, id) {
      state.schoolId = id
      localStorage.setItem('schoolId', state.schoolId)
    },
    changeAdminId(state, id) {
      state.adminId = id
      localStorage.setItem('adminId', state.adminId)
    },
    changeComponentName(state, name) {
      state.componentName = name
    },
    changeArticle(state, article) {
      state.article = article
    }
  },
  actions: {
  },
  getters: {
    studentId: (state) => state.studentId,
    coachId: (state) => state.coachId,
    schoolId: (state) => state.schoolId,
    adminId: (state) => state.adminId
  }
})
