          <p class="break">··········IMPORTANT NOTICE ··········</p>
          <div id="pcnotice">
            <el-col :span="24" v-for="(item, index) in notices" :key="index">
              <router-link :to=" 'noticeInfo/' + item.id">
                <div class="news">
                  <el-card class="box-card">
