<script>
export default {
  data: function () {
    return {
      isCollapsed: false,
      carouselHeight: '',
      news: [],
      notices: []
    }
  },
  created () {
    this.setHeight()
  },
  mounted() {
    this.setHeight()
    this.getArticle()
  },
  methods: {
    setHeight() {
      var val = document.body.clientWidth
      const def = 1024 // 默认宽度
      if (val < def) {
        this.carouselHeight = '150px'
        document.getElementById('pc').style.display = 'none'
        document.getElementById('pcnotice').style.display = 'none'
      } else {
        this.carouselHeight = '400px'
        document.getElementById('phone').style.display = 'none'
        document.getElementById('phonenotice').style.display = 'none'
      }
    },
    moreNews() {
      this.$router.push('/news')
    },
    moreNotices() {
      this.$router.push('/notices')
    },
    async getArticle () {
      const { data: res } = await this.$http.get('user/article')
      if (res.status === 605) {
        var data = res.data.reverse()
        for(var i = 0; i < data.length; i++) {
          if(this.news.length <= 10 || this.notices.length <= 10) {
            if(data[i].tag === 0) {
              if(this.news.length <= 10) this.news.push(data[i])
              else continue
            } else {
              if(this.notices.length <= 10) this.notices.push(data[i])
              else continue
            }
          }
        }
      }
    },
    formatDate (date) {
      date = new Date(date)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return y + '-' + m + '-' + d
    }
  }
}
</script>

