<template>
  <el-container class="home_container">
    <el-header>
      <div>
        <el-button type="primary" icon="el-icon-s-operation" @click="toggleCollapse" circle></el-button>
        <span>国际大学生程序设计竞赛管理系统</span>
      </div>
      <el-button @click="logout" type="primary" icon="el-icon-switch-button"> 安全退出</el-button>
    </el-header>
    <el-container>
      <el-aside :width="isCollapsed? '0' :'250px'">
        <el-menu background-color="#fff"  text-color="#000"  active-text-color="cornflowerblue" :collapse="isCollapsed"
        :collapse-transition="false" router>
          <el-menu-item index="/student/details" @click="setIsCollapsed">
            <i class="iconfont icon-ziliao"></i>
            <span slot="title">我的信息</span>
          </el-menu-item>
          <el-menu-item index="/student/changepwd" @click="setIsCollapsed">
            <i class="iconfont icon-zhiwen"></i>
            <span slot="title">修改密码</span>
          </el-menu-item>
          <el-menu-item index="/student/contests" @click="setIsCollapsed">
            <i class="iconfont icon-bisai"></i>
            <span slot="title">我参加的团队赛</span>
          </el-menu-item>
          <el-menu-item index="/student/soloContests" @click="setIsCollapsed">
            <i class="iconfont icon-bisai"></i>
            <span slot="title">个人赛</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return{
      isCollapsed: false
    }
  },
  created() {
    this.setIsCollapsed()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setIsCollapsed()
      })()
    }
  },
  methods: {
    setIsCollapsed() {
      var val = document.body.clientWidth
      const def = 1024 // 默认宽度
      if (val < def) {
        this.isCollapsed = true
      } else {
        this.isCollapsed = false
      }
    },
    async logout() {
      const { data: res } = await this.$http.post('user/logout')
      if(res.status === 600) this.$router.push('/login')
    },
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed
    }
  }
}
</script>

