<template>
  <div class="container">
    <div class="box">
      <div class="title">
        <h2>{{article.title}}</h2>
        <p>发布时间：{{formatDate(article.latestEditTime)}} | 作者：admin</p>
      </div>
      <div class="ql-editor">
        <div class="content" v-html="article.content"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      article: {
        title: '',
        latestEditTime: '',
        content: ''
      }
    }
  },
  created () {
    var articleId = this.$route.params.id
    this.getArticle(articleId)
  },
  methods: {
    async getArticle (articleId) {
      const { data: res } = await this.$http.get('user/article')
      if (res.status === 605) {
        var data = res.data.reverse()
        for(var i = 0; i < data.length; i++) {
          if(articleId === data[i].id.toString() && data[i].tag === 0) {
            this.article = data[i]
            break
          }
        }
      }
    },
    formatDate (date) {
      date = new Date(date)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return y + '-' + m + '-' + d
    }
  }
}
</script>
